import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";

import DOMPurify from "dompurify";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleDown,
  faAngleUp,
  faArrowDown,
  faArrowUp,
} from "@fortawesome/free-solid-svg-icons";
import { Sparklines, SparklinesLine } from "react-sparklines";
import Pagination from "@material-ui/lab/Pagination";
import PaginationItem from "@material-ui/lab/PaginationItem";
import MakeFavourites from "./make-favourite";
import MakeAlerts from "./make-alert";
import { withStyles } from "@material-ui/core";
import Loader from "../shared/loader";
import { NumberFormat } from "../../common/number-format";
import axios from "axios";

const useStyles = (theme: any) => ({
  root: {
    marginLeft: theme.spacing(3),
    "& > *": {
      marginTop: theme.spacing(2),
      padding: theme.spacing(1),
    },
  },
});

interface LongTermInvestmentsProps {
  webSocket: any;
  checkLogin: () => void;
  classes: any;
  checkPayment:()=>void;
}

const myHTML = `<p class="title-main"></p>
    				
<ul class="li-link">
<li>VeroxAI is on the lookout for the perfect assets for you to HODL.</li>
<li>Discover the projects that go beyond current hype.</li>
<li>Our algorithm intelligently assesses millions of end points to find the projects with excellent fundamentals, innovative underlying technologies, solid use cases, potential market adoption, high expected profit and more. 		</li>
<p class="desc-main coming-soon-data">Coming Soon</p> `;

const mySafeHTML = DOMPurify.sanitize(myHTML, {
  ALLOWED_TAGS: ["h1", "p", "span", "ul", "li"],
  ALLOWED_ATTR: ["style", "class"],
});

interface LongTermInvestmentsState {
  longTermCoinsData: any[];
  isLongTermCoinsDataLoading: boolean;
  page: number;
  asc: boolean;
}

interface LongTermInvestmentsProps {
  webSocket: any;
  checkLogin: () => void;
}
// component for best long term investments
class LongTermInvestments extends React.Component<
  LongTermInvestmentsProps,
  LongTermInvestmentsState
> {
  constructor(props: LongTermInvestmentsProps) {
    super(props);
    this.state = {
      longTermCoinsData: [],
      isLongTermCoinsDataLoading: true,
      page: 1,
      asc: false,
    };
    props.checkLogin();
    props.checkPayment();
  }

  componentDidMount() {
    document.title = "Long Term Crypto Investment | Verox";

    axios
      .get(
        "https://api.veroxai.io/watchlist/?ids=bitcoin, ethereum, binancecoin, matic-network, uniswap, enjincoin, arweave, theta-token, kusama"
      )
      .then((response: any) => {
        if (response.data && response.data.coins) {
          this.setState({
            longTermCoinsData: response.data.coins,
            isLongTermCoinsDataLoading: false,
          });
        }
      });

    // this.props.webSocket.emit("getLongTermCoins", {});
    // this.props.webSocket.on("longTermCoins", (response: any) => {
    //   if (response.data && response.data.data) {
    //     this.setState({
    //       longTermCoinsData: response.data.data,
    //       isLongTermCoinsDataLoading: false,
    //     });
    //     this.props.webSocket.removeAllListeners("longTermCoins"); // this is just one-time listener
    //   }
    // });
  }

  render() {
    let { classes } = this.props;
    console.log("check", this.state.longTermCoinsData);

    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4 style={{ fontSize: "2.25rem" }}>
                    Long Term Crypto Investment
                  </h4>
                  <p>View coins for long term investment</p>
                </div>
              </Container>
            </Navbar>

            {/* <Row className="terms-of-use">
              <Col xs={12} sm={12} xl={12}>
                <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
              </Col>
            </Row> */}

            <Row className="justify-content-md-center main-markets-table-head">
              <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                <h5 className="main-head-coin-d">Coin</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Price</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>24H Change</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5
                  // onClick={() => this.sortData()}
                  style={{ cursor: "pointer" }}
                >
                  Volume&nbsp;&nbsp;
                  <FontAwesomeIcon
                    icon={this.state.asc ? faArrowUp : faArrowDown}
                  />
                </h5>
              </Col>
            </Row>

            <div
              // style={{ overflowY: "scroll", maxHeight: "81rem" }}
              className="main-table-cap"
              id="main-table-cap"
            >
              {this.state.isLongTermCoinsDataLoading && <Loader />}

              {!this.state.isLongTermCoinsDataLoading &&
                this.state.longTermCoinsData.length > 0 &&
                this.state.longTermCoinsData.map((coin: any, index: number) => {
                  return (
                    <Row
                      key={coin.id}
                      className="justify-content-md-center"
                      onClick={() => {
                        window.location.hash =
                          "/coin/" + coin.symbol + "/" + coin.id;
                      }}
                    >
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                          <table className="main-coin-name">
                            <tr>
                              <td>
                                <img src={coin.image} alt={coin.name} />
                              </td>
                              <td>
                                <h6>
                                  {coin.name} <br /> <span>{coin.symbol}</span>
                                </h6>{" "}
                              </td>
                            </tr>
                          </table>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(coin.current_price)}
                          </p>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <div className="chart-coin-name">
                            {coin.market_cap_change_percentage_24h > 0 ? (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleUp}
                                  className={`text-success me-1`}
                                />
                                <span className={`text-success fw-bold`}>
                                  {NumberFormat(
                                    coin.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            ) : (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleDown}
                                  className={`text-danger me-1`}
                                />
                                <span className={`text-danger fw-bold`}>
                                  {NumberFormat(
                                    coin.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            )}
                            <Sparklines data={coin.sparkline_in_7d.price}>
                              <SparklinesLine color={"rgb(18,199,255)"} />
                            </Sparklines>
                          </div>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <p className="PriceData">
                            {NumberFormat(coin.total_volume)}
                          </p>
                        </Col>
                    </Row>
                  );
                })}

              {/*  TODO: fix this */}
              {/* {!this.state.isLongTermCoinsDataLoading && this.state.longTermCoinsData.length > 0 && (
                <ScrollUpButton ContainerClassName="ScrollUpButton__Container" TransitionClassName="ScrollUpButton__Toggled">
                  <p> <FontAwesomeIcon icon={faAngleUp} /> Scroll To Top  </p>
                </ScrollUpButton> 
              )} */}

              {!this.state.isLongTermCoinsDataLoading &&
                this.state.longTermCoinsData.length === 0 && (
                  <Row className="justify-content-md-center">
                    <Col xs={12} sm={12} xl={12}>
                      <div className="no-data">
                        <h6>No data found for markets.</h6>
                      </div>
                    </Col>
                  </Row>
                )}
            </div>
            {/* {this.state.longTermCoinsData.length !== 0 && (
                <div className={classes.root}>
                <Pagination count={} color="primary" page={this.state.page} onChange={(event,page) =>this.changePage(event,page) } 
                  renderItem={(item) => (
                    <PaginationItem
                      style={{ color: "white" }}
                      {...item}
                    />
                    )}
                size="large"/>
                </div>
              )} */}
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default withStyles(useStyles)(LongTermInvestments);
