import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";
import { Sparklines, SparklinesLine } from "react-sparklines";
import { faAngleDown, faAngleUp,faTrashAlt } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Footer from "../shared/footer";
import Loader from "../shared/loader";
import { NumberFormat } from "../../common/number-format";
import React from "react";
import Sidebar from "../shared/sidebar";

interface NotificationsState {
  alertsDataPrice: any[],
  isAlertsDataLoading: boolean,
  tokens: any,

  
}

interface NotificationsProps {
    webSocket: any,
    checkLogin: ()=>void,
    checkPayment:()=>void
}

// dashboard component
class Notifications extends React.Component<NotificationsProps, NotificationsState> {

  constructor(props: NotificationsProps) {
    super(props);
    this.props.checkLogin();
    props.checkPayment();
    this.state = {
      alertsDataPrice:[],
      isAlertsDataLoading: true,
      tokens:[]
    };
  }

  update(){
      this.props.webSocket.emit("getUserDetails", {
        userId: localStorage.getItem("verox_uid")
      });


    
    this.props.webSocket.on("userDetails", (response: any) => {

      let rdata =   response.data;
      this.setState({alertsDataPrice: rdata.notification, isAlertsDataLoading: false});
        
    });

  }
  componentDidMount() {
    document.title = "Notifications | Verox";
    this.update();


  }

  clear(){
    this.props.webSocket.emit("clearUserNotification", {
      userId: localStorage.getItem("verox_uid")
    });
    
  }
  timeSince(date:any) {
    const today:any = new Date();
    var seconds = Math.floor((today.getTime() - date) / 1000);
  
    var interval = seconds / 31536000;
  
    if (interval > 1) {
      return Math.floor(interval) + " years";
    }
    interval = seconds / 2592000;
    if (interval > 1) {
      return Math.floor(interval) + " months";
    }
    interval = seconds / 86400;
    if (interval > 1) {
      return Math.floor(interval) + " days";
    }
    interval = seconds / 3600;
    if (interval > 1) {
      return Math.floor(interval) + " hours";
    }
    interval = seconds / 60;
    if (interval > 1) {
      return Math.floor(interval) + " minutes";
    }
    return Math.floor(seconds) + " seconds";
  }
  render() {
    return (
        <>
        <Sidebar webSocket={this.props.webSocket}/>
          <div className="app-container-with-sidebar">
            <div className="component">
              <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4 style={{ fontSize: "2.25rem"}}>Notifications</h4>
              {/*<h4>Hi, {userInfo.username}!</h4>*/}
              <p>List for Notifications</p>
            </div>
          </Container>
        </Navbar>

        
         
        <div className="header-class justify-content-between w-100">
              <p className="back-arrow" onClick={()=>this.clear()}><FontAwesomeIcon
                  icon={faTrashAlt}/> Clear</p>
        </div>
            
          <div className="main-table-cap">
            {this.state.isAlertsDataLoading && (
              <Loader />
            )}
            {!this.state.isAlertsDataLoading && this.state.alertsDataPrice && this.state.alertsDataPrice.length > 0 && this.state.alertsDataPrice.map((data,index) => {
           
              return(
                  <Row key={data.coin} className="justify-content-md-left" >
                      <Col xs={9} sm={9} xl={9} className="mb-3">
                      <p className="PriceData" style={{textAlign:'left',cursor:'default'}}>{data.title}</p>
                      </Col>
                      <Col xs={3} sm={3} xl={3} className="mb-3">

                      <p className="PriceData" style={{textAlign:'right',cursor:'default'}}>{this.timeSince(data.ts)}</p>
                      </Col>
                  </Row>    
              )
            })}

            {!this.state.isAlertsDataLoading && (!this.state.alertsDataPrice || this.state.alertsDataPrice.length === 0) && (
              <Row className="justify-content-md-center">
                <Col xs={12} sm={12} xl={12} >
                  <div className="no-data">                  
                    <h6>No data found for notifications.</h6>
                  </div>
                </Col>              
              </Row>    
            )}
          </div>

          
          


        </div>
        <Footer/>
        </div>
      </>
    );
  }
}

export default Notifications;
