import "firebase/auth";

import { Button, Modal } from "@themesberg/react-bootstrap";
import {
  Redirect,
  Route,
  Switch,
  BrowserRouter as Router,
} from "react-router-dom";
import history from "./history";
// import { browserh } from 'react-router-dom'

import AboutUs from "./pages/static/about-us";
import AdminCalls from "./pages/admin-calls";
import Alerts from "./pages/alerts";
import Auth from "./pages/auth";
import BiggestVolumeChanges from "./pages/biggest-volume-changes";
import CoinDetail from "./pages/coin-details";
import CookiePolicy from "./pages/static/cookie-policy";
import Dashboard from "./pages/dashboard";
import Disclaimer from "./pages/static/disclaimer";
import FAQ from "./pages/faq";
import { Image } from "@themesberg/react-bootstrap";
import LongTermInvestments from "./pages/long-term-investments";
import Markets from "./pages/markets";
import MostMentioned from "./pages/most-mentioned";
import News from "./pages/news";
import Notifications from "./pages/notifications";
import PaymentPlan from "./pages/paymentplan";
import PrivacyPolicy from "./pages/static/privacy-policy";
import React from "react";
import { Routes } from "../common/routes";
import ScoreAnalyser from "./pages/score-analyzer";
import ScoreAnalyserDetail from "./pages/score-analyser-detail";
import TermsOfUse from "./pages/static/terms-of-use";
import TopPicks from "./pages/top-picks";
import TradingBot from "./pages/trading-bot";
import VeroxLogo from "../assets/images/logo.png";
import YieldFarming from "./pages/yield-farming";
import firebase from "firebase/app";
import openSocket from "socket.io-client";
import MyFavourites from "./pages/my-favourites";
import Settings from "./pages/settings";

import Snackbar from "@material-ui/core/Snackbar";
import Toast from "react-bootstrap/Toast";
import ToastContainer from "react-bootstrap/ToastContainer";
import axios from "axios";

import "firebase/messaging";
import RecentSignals from "./pages/recent-signals";
import ResetPassword from "./pages/reset-password";
import { ThemeProvider } from "../theme/themeContext";

interface AppState {
  isAuthModalVisible: boolean;
  signUpModal: boolean;
  open: boolean;
  title: string;
  body: string;
  vertical: "top" | "bottom";
  horizontal: "left" | "center" | "right";
  paymentstatus: any;
}

interface AppProps {}

// Outermost component which contains the Sidebar, footer and other pages based on the route
class App extends React.Component<AppProps, AppState> {
  webSocket: any;

  constructor(props: AppState) {
    super(props);

    // creating a singleton websocket connection which will be passed to all children components
    this.webSocket = openSocket("https://apiv2.veroxai.io");
    this.webSocket.connect();

    this.state = {
      paymentstatus: {},
      isAuthModalVisible: false,
      signUpModal: false,
      open: false,
      title: "",
      body: "",
      vertical: "top",
      horizontal: "right",
    };

    if (!firebase.apps.length) {
      // Your web app's Firebase configuration
      var firebaseConfig = {
        apiKey: "AIzaSyDlBSWROKMKmC9saAXRe9BkoGnh8Uf-URk",
        authDomain: "verox-41537.firebaseapp.com",
        projectId: "verox-41537",
        storageBucket: "verox-41537.appspot.com",
        messagingSenderId: "437685966979",
        appId: "1:437685966979:web:f898988d18d048b71aa3b6",
        measurementId: "G-YK5WM8E7NP",
      };
      // Initialize Firebase
      firebase.initializeApp(firebaseConfig);
    }
  }

  componentDidMount() {
    // Accessing scss variable "--background-color"
    // and "--text-color" using plain JavaScript
    // and changing the same according to the state of "darkTheme"
    // const root = document.documentElement;
    // let variables = getComputedStyle(root)
    // root?.style.setProperty(
    //   "--bs-body-bg","#040a16"
    // );
    let messaging: any = null;
    if (firebase.messaging.isSupported()) {
      messaging = firebase.messaging();
      if (localStorage.getItem("verox_uid")) {
        this.recheck_payment();
        messaging
          .getToken({
            vapidKey:
              "BIcR735h4gq3u8u_PMCzH8gfOPIMN52LyghMX5eb-w6gKlmIKeN7agm-bWPgRes8jqf3lRxc7umUHfW8Tx9DbvA",
          })
          .then((currentToken: any) => {
            if (currentToken) {
              const userData = {
                userId: localStorage.getItem("verox_uid"),
                name: localStorage.getItem("verox_user_display_name"),
                email: localStorage.getItem("verox_email"),
                mobile: localStorage.getItem("verox_phone"),
              };

              this.webSocket.emit("pushUserDetials", userData);

              this.webSocket.emit("pushDeviceToken", {
                userId: localStorage.getItem("verox_uid"),
                web: { fcm_token: currentToken },
              });

              const onMessageListener = () =>
                new Promise((resolve) => {
                  messaging.onMessage((payload: any) => {
                    resolve(payload);
                  });
                });

              onMessageListener()
                .then((payload: any) => {
                  //setShow(true);
                  //setNotification({title: payload.notification.title, body: payload.notification.body})
                  this.setState({
                    open: true,
                    title: payload.notification.title,
                    body: payload.notification.body,
                  });
                  console.log("got push notification ", payload);
                })
                .catch((err) => console.log("failed: ", err));

              // Track the token -> client mapping, by sending to backend server
              // show on the UI that permission is secured
            } else {
              console.log(
                "No registration token available. Request permission to generate one."
              );

              // shows on the UI that permission is required
            }
          })
          .catch((err: any) => {
            console.log("An error occurred while retrieving token. ", err);
            // catch error while creating client token
          });
      } else {
        console.log("removed verox_uid", localStorage.getItem("verox_uid"));
      }
    }
  }
  handleClose() {
    this.setState({ open: false });
  }

  recheck_payment() {
    axios
      .get(
        "https://ec2-3-138-183-250.us-east-2.compute.amazonaws.com:2083/getstatus?veroxuid=" +
          localStorage.getItem("verox_uid")
      )
      .then((response) => {
        console.log("credit status", response);
        this.setState({ paymentstatus: response.data.result });
        this.checkPayment(); 
      });
  }
  checkLogin() {
    if (!localStorage.getItem("verox_uid")) {
      window.location.hash = Routes.Markets.path;
      this.showAuthModal();
    }
  }

  checkPayment(minPlan=1) { 
    return;
    const now = new Date().getTime();
    console.log('this.state.paymentstatus',this.state.paymentstatus,!this.state.paymentstatus.enddate,now > this.state.paymentstatus.enddate,this.state.paymentstatus.plan<minPlan,now,this.state.paymentstatus.enddate);

    if (
      !this.state.paymentstatus.enddate ||
      now > this.state.paymentstatus.enddate ||
      this.state.paymentstatus.plan<minPlan
    ) {
      window.location.hash = Routes.PaymentPlan.path;
    }
  }
  showAuthModal() {
    this.setState({ isAuthModalVisible: true });
    this.setState({ signUpModal: false });
  }
  goPaymentPlan() {
    window.location.hash = Routes.PaymentPlan.path;
  }
  showSignUpAuthModal() {
    this.setState({ isAuthModalVisible: true });
    this.setState({ signUpModal: true });
  }

  hideAuthModal() {
    this.setState({ isAuthModalVisible: false });
  }

  render() {
    return (
      <>
      <ThemeProvider>
        {!localStorage.getItem("verox_uid") && (
          <div>
            <Button
              style={{ marginRight: "160px" }}
              variant="primary"
              className="login-button"
              onClick={() => this.showAuthModal()}
            >
              Login
            </Button>
            <Button
              style={{ width: "8rem" }}
              variant="primary"
              className="login-button"
              onClick={() => this.showSignUpAuthModal()}
            >
              Sign Up
            </Button>
          </div>
        )}
        <ToastContainer
          className="p-3"
          position="top-end"
          style={{
            zIndex: 999999,
          }}
        >
          <Toast
            onClose={() => this.handleClose()}
            show={this.state.open}
            delay={6000}
            autohide
            bg="light"
          >
            <Toast.Header closeButton={false}>
              <img src="/logo32.png" className="rounded me-2" alt="" />
              <strong className="me-auto">VEROX</strong>
              <small>just now</small>
            </Toast.Header>
            <Toast.Body>
              <h6>{this.state.title}</h6>
              <p>{this.state.body}</p>
            </Toast.Body>
          </Toast>
        </ToastContainer>

        <div className="App">
          <Route exact path="/">
            <Redirect to={Routes.Markets.path} />
          </Route>
          {/* <Switch> */}
          <Route exact path={Routes.Markets.path}>
            <Markets
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.ResetPassword.path}>
            <ResetPassword
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route path={Routes.CoinDetail.path}>
            <CoinDetail
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.Dashboard.path}>
            <Dashboard
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.MyAlerts.path}>
            <Alerts
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.TopPicks.path}>
            <TopPicks
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.MostMentioned.path}>
            <MostMentioned
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.BiggestVolumeChanges.path}>
            <BiggestVolumeChanges
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.News.path}>
            <News
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.YieldFarming.path}>
            <YieldFarming
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.ScoreAnalyser.path}>
            <ScoreAnalyser
              checkPayment={() => this.checkPayment(2)}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.ScoreAnalyserDetail.path}>
            <ScoreAnalyserDetail
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.AdminCalls.path}>
            <AdminCalls
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.LongTermCrypto.path}>
            <LongTermInvestments
              checkPayment={() => this.checkPayment(2)}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.AutomatedTrade.path}>
            <TradingBot
              checkPayment={() => this.checkPayment(2)}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.RecentSignals.path}>
            <RecentSignals
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.FAQ.path}>
            <FAQ
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.AboutUs.path}>
            <AboutUs webSocket={this.webSocket} />
          </Route>
          <Route exact path={Routes.CookiePolicy.path}>
            <CookiePolicy webSocket={this.webSocket} />
          </Route>
          <Route exact path={Routes.Disclaimer.path}>
            <Disclaimer webSocket={this.webSocket} />
          </Route>
          <Route exact path={Routes.PrivacyPolicy.path}>
            <PrivacyPolicy webSocket={this.webSocket} />
          </Route>
          <Route exact path={Routes.TermsOfUse.path}>
            <TermsOfUse webSocket={this.webSocket} />
          </Route>
          <Route exact path={Routes.MyFavourites.path}>
            <MyFavourites
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.Settings.path}>
            <Settings
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.Notifications.path}>
            <Notifications
              checkPayment={() => this.checkPayment()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>
          <Route exact path={Routes.PaymentPlan.path}>
            <PaymentPlan
              recheckpayment={this.recheck_payment}
              showSignUpAuthModal={() => this.showSignUpAuthModal()}
              checkLogin={() => this.checkLogin()}
              webSocket={this.webSocket}
            />
          </Route>

          {/* </Switch> */}
        </div>

        <Modal
          className="auth-modal"
          size="sm"
          centered
          show={this.state.isAuthModalVisible}
          backdrop="static"
          onHide={() => {
            // if (!localStorage.getItem("verox_uid")) {
            //   window.location.href = Routes.Markets.path;
            // }
            this.hideAuthModal();
          }}
        >
          <Modal.Header closeButton></Modal.Header>
          <Modal.Body>
            <div className="cropped">
              <Image src={VeroxLogo} height={100} />
            </div>
            <Auth
              firebase={firebase}
              signUpModal={this.state.signUpModal}
              hideAuthModal={() => this.hideAuthModal()}
              webSocket={this.webSocket}
            />
          </Modal.Body>
        </Modal>
        </ThemeProvider>
      </>
    );
  }
}

export default App;
