import { Alert, Button, Form,NavItem } from "@themesberg/react-bootstrap";
import React from "react";
import {useRef } from "react";
import googleLogo from "../../assets/images/google-logo.png"
import facebookLogo from "../../assets/images/f_logo.png"
import phoneLogo from "../../assets/images/phone.svg"
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input'
import { Link } from "react-router-dom";
import axios from "axios";



interface AuthState {
    email: string,
    password: string,
    password2: string,
    mobile: string,
    mobilecode: string,
    otp: string,
    isEmailSignInVisible: boolean,
    isMobileSignInVisible: boolean,
    isEmailSignUpVisible: boolean,
    errorMessage: string,
    successMessage: string,
    isChecked:boolean,
    reRef :any,
    name:any
}

interface AuthProps {
  webSocket: any,
  hideAuthModal: () => void,
  firebase: any
  signUpModal: boolean
}

class Auth extends React.Component<AuthProps, AuthState> {
  otpValidator: any;
  recaptcha: any;

  constructor(props: AuthProps) {
    super(props);
    this.state = {
        email: "",
        password: "",
        password2: "",
        mobile: "",
        mobilecode: "",
        otp: "",
        isEmailSignInVisible: false,
        isMobileSignInVisible: false,
        isEmailSignUpVisible: false,
        errorMessage: "",
        successMessage: "",
        isChecked: false,
        reRef: {'s':'s'},
        name:''
    }
  }

  componentDidMount() {
    if (this.props.signUpModal) {
      this.showSignUp();
    }
    this.props.firebase.auth().onAuthStateChanged((user:any) => {
      if (user && localStorage.getItem("verox_signin")) {
        if(localStorage.getItem("verox_signin")=="email"  && !user.emailVerified){
          this.setState({ errorMessage: "Please verify your email before signin." });
          localStorage.removeItem("verox_uid");
          localStorage.removeItem("verox_user_display_name");
          localStorage.removeItem("verox_email");
          localStorage.removeItem("verox_phone");
          localStorage.removeItem("verox_signin");
          this.props.firebase.auth().signOut();
        }else{
          //console.log("logged.",localStorage.getItem("verox_signin"),user.emailVerified);
          window.location.hash = "/dashboard";
          window.location.reload();
        }
      }
    })
    
  }

  signInWithGoogle() {
    this.setState({ errorMessage: "", successMessage:""});
    var provider = new this.props.firebase.auth.GoogleAuthProvider();
    this.props.firebase.auth()
      .signInWithPopup(provider)
      .then((result: any) => {
        console.log('gmail result=',result)
        localStorage.setItem('verox_uid', result.user?.uid + "");
        localStorage.setItem('verox_user_display_name', result.user?.displayName + "");
        localStorage.setItem("verox_email",result.additionalUserInfo.profile.email);
        localStorage.setItem("verox_signin",'gmail');
        
        //window.location.hash = "/dashboard";
        //window.location.reload();
      }).catch((error: any) => {
        console.log(error);
        this.setState({ errorMessage: error.message });
      });
  }

  signInWithFacebook() {
    this.setState({ errorMessage: "", successMessage:""});

    var provider = new this.props.firebase.auth.FacebookAuthProvider();
    this.props.firebase.auth()
      .signInWithPopup(provider)
      .then((result: any) => {
        localStorage.setItem('verox_uid', result.user?.uid + "");
        localStorage.setItem('verox_user_display_name', result.user?.displayName + "");
        localStorage.setItem("verox_email",result.user?.email);
        localStorage.setItem("verox_signin",'facebook');

        //window.location.hash = "/dashboard";
        //window.location.reload();
      }).catch((error: any) => {
        console.log(error);
        this.setState({ errorMessage: error.message });
      });
  }



  signUpWithUsernamePassword() {
    this.setState({ errorMessage: "", successMessage:""});
    if (this.state.password !== this.state.password2) {
      this.setState({ errorMessage: "Passwords do not match." });
      return;
    } else if (!(this.state.isChecked)) {

      this.setState({ errorMessage: "Please accept terms and conditions" });
      return;

    }
    // if (!this.recaptcha) {

    //   // var recaptcha1 = new this.props.firebase.auth.RecaptchaVerifier('captchas-container');
    //   this.recaptcha = new this.props.firebase.auth.RecaptchaVerifier('captchas-container', {
    //     'size': 'invisible',
    //     'callback': (response: any) => {
    //       console.log('response', response);
    //       this.props.firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.password,)
    //       .then((userCredential: any) => {
    //         userCredential.user.updateProfile({
    //           displayName: this.state.name
    //         })
    //         this.props.webSocket.on("userDetails", (data: any) => {
    //           console.log("data");
    //           console.log(data);
    //           this.props.webSocket.removeAllListeners("userDetails");
        
    //         });
    //         this.props.webSocket.emit("pushUserDetials", {
    //           userId: userCredential.user.uid,
    //           name: this.state.name,
    //           email: this.state.email,
    //         });
    //             this.props.firebase.auth().currentUser.sendEmailVerification().then((response: any) => {
    //               this.props.firebase.auth().signOut();
    //               this.showSignInOptions();
    //               this.setState({successMessage: "Verification Email has been sent to " + this.state.email});
    //               setTimeout(()=>{
    //                 this.setState({successMessage: ""});
    //               }, 4000)
    //             })
    //         .catch((error: any) => {
    //           console.log(error);
    //             this.setState({errorMessage: error.message});
    //         });
    //       })
    //       .catch((error: any) => {
    //         console.log(error);
    //           this.setState({errorMessage: error.message});
    //       });
            
    //     },
    //     'expired-callback':(response:any) =>{
          
    //     }
        
    //   });
    //   this.recaptcha.verify();
      
      
    // }
    
    const formData = new FormData();
    formData.append('email', this.state.email)
    formData.append('password', this.state.password)
    formData.append('cpassword', this.state.password2)
    axios
      .post(
        "https://auth.veroxai.io/register/",
        formData
      )
      .then((result: any) => {
        console.log("register: ", result)

        axios
        .post(
          "https://auth.veroxai.io/register/",
          formData
        )
      })
      .catch((error: any) => {
        console.log(error);
        this.setState({ errorMessage: error.message });
      });
  }

  signInWithUsernamePassword() {
    this.setState({ errorMessage: "", successMessage:""});
    
    const formData = new FormData();
    formData.append('email', this.state.email)
    formData.append('password', this.state.password)
    axios
      .post(
        "https://auth.veroxai.io/auth/",
        formData
      )
      .then((result: any) => {
        console.log("login: ", result)
        localStorage.setItem('verox_uid', result.data.access + "");
        localStorage.setItem('verox_user_display_name', (this.state.email) + "");
        localStorage.setItem("verox_email",this.state.email);
        localStorage.setItem("verox_signin",'email');

        window.location.href = "/dashboard";
        window.location.reload();
      })
      .catch((error: any) => {
        console.log(error);
        this.setState({ errorMessage: error.message });
      });

    // this.props.firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password)
    //   .then((result: any) => {
    //     console.log('result=',result.user);
        
    //     localStorage.setItem('verox_uid', result.user?.uid + "");
    //     localStorage.setItem('verox_user_display_name', (result.user?.displayName ? result.user?.displayName : result.user?.email) + "");
    //     localStorage.setItem("verox_email",this.state.email);
    //     localStorage.setItem("verox_signin",'email');

    //     //window.location.href = "/dashboard";
    //     //window.location.reload();
    //   })
    //   .catch((error: any) => {
    //     console.log(error);
    //     this.setState({ errorMessage: error.message });
    //   });
  }

  signInWithMobile() {
    this.setState({ errorMessage: "", successMessage:""});

    if (!this.recaptcha) {
      this.recaptcha = new this.props.firebase.auth.RecaptchaVerifier('captcha-container', {
        'size': 'invisible',
        'callback': (response: any) => {
        }
      });
    }

    if(this.recaptcha) {
      this.props.firebase.auth().signInWithPhoneNumber(`${this.state.mobilecode} ${this.state.mobile}`, this.recaptcha)
        .then((confirmationResult: any) => {
          // SMS sent. Prompt user to type the code from the message, then sign the
          // user in with confirmationResult.confirm(code)
          console.log(confirmationResult);
          this.otpValidator = confirmationResult;
          this.setState({ successMessage: "OTP Sent to your mobile number" });
          // ...
        }).catch((error: any) => {
          // Error; SMS not sent
          console.log(error);
          this.setState({ errorMessage: error.message });
        });
    }

  }

  validateMobileOtp() {
        this.setState({ errorMessage: "", successMessage:""});

    if (this.otpValidator) {
      this.otpValidator.confirm(this.state.otp)
        .then((result: any) => {
          localStorage.setItem('verox_uid', result.user?.uid + "");
          localStorage.setItem('verox_user_display_name',(result.user?.displayName ? result.user?.displayName : result.user?.phoneNumber) + "");
          localStorage.setItem("verox_phone",this.state.mobile);
          localStorage.setItem("verox_signin",'mobile');

          //window.location.hash = "/dashboard";
          //window.location.reload();
        })
        .catch((error: any) => {
          console.log(error);
          this.setState({ errorMessage: error.message });
        });
    }
  }

  showSignInWithMobile() {
    this.setState({
      isEmailSignInVisible: false,
      isMobileSignInVisible: true,
      isEmailSignUpVisible: false,
      errorMessage: "",
      successMessage: ""
    });
  }

  showSignInWithMobileOtp() {
    this.setState({
      isEmailSignInVisible: false,
      isMobileSignInVisible: true,
      isEmailSignUpVisible: false,
      errorMessage: "",
      successMessage: ""
    });
  }

  showSignInOptions() {
    this.setState({
      isEmailSignInVisible: false,
      isMobileSignInVisible: false,
      isEmailSignUpVisible: false,
      errorMessage: "",
      successMessage: ""
    });
  }

  showSignUp() {
    this.setState({
      isEmailSignInVisible: false,
      isMobileSignInVisible: false,
      isEmailSignUpVisible: true,
      errorMessage: "",
      successMessage: ""
    });
  }

  showSignInWithUsernamePassword() {
    this.setState({
      isEmailSignInVisible: true,
      isMobileSignInVisible: false,
      isEmailSignUpVisible: false,
      errorMessage: "",
      successMessage: ""
    });
  }
//                    <div className="login-card"> <Button style={{border:"none"}} onClick={()=>this.showSignInWithUsernamePassword()}>Email</Button></div>


  render() {
    return (
      <>
      <div id="captchas-container" ></div>

        { !this.state.isEmailSignInVisible && !this.state.isMobileSignInVisible && !this.state.isEmailSignUpVisible && (
            <>
                <h4>Login to Veroxai</h4>
                <div className="buttonDiv">
                    <div className="login-card"><Form.Control type="email" placeholder="Enter email" onChange={(event) => this.setState({ email: event.target.value })} /> </div>
                    <br />
                    <div className="login-card"> <Form.Control type="password" placeholder="Password" onChange={(event) => this.setState({ password: event.target.value })} /></div>
                    <br />
                    
                    <div className="login-card"><Button style={{border:"none"}} onClick={() => this.signInWithUsernamePassword()}>Sign In</Button> </div>
                    <Link to="/reset-password" style={{color:"rgb(19,199,255)",display:"inline-block",margin:"10px 0"}}
                    onClick={
                      ()=>{
                        this.props.hideAuthModal()
                      }
                    }
                    >Forgot Password?</Link>
                    <br/>  OR  <br/> <br/> 
                  
                    <div className="login-card"> <Button style={{paddingRight:'40px',border:"none"}} onClick={()=>this.signInWithGoogle()}><img src={googleLogo} /><span style={{marginLeft:"20px"}}>Login with Google</span></Button></div>
                    <br/>
                    <div className="login-card"><Button style={{paddingRight:'30px',border:"none"}} onClick={()=>this.signInWithFacebook()}><img src={facebookLogo}  /><span style={{marginLeft:"20px"}}>Login with Facebook</span></Button> </div>
                    <br/>
                    <div className="login-card">
                         <Button style={{paddingLeft:'30px',border:"none"}}  onClick={()=>this.showSignInWithMobile()}><img src={phoneLogo}  /><span style={{marginLeft:"20px"}}>Login with Mobile Number</span></Button>

                    </div> 
                    
                    
                    
                    <br/> <br/>
                    <Button variant="link" onClick={() => this.showSignUp()}>Don&apos;t have an account? Sign Up</Button>
                </div>
            </>
        )}
        
        {this.state.isEmailSignUpVisible && (
          <>
            <h4>Create Account</h4>
            <div className="buttonDiv">
              <div className="login-card"><Form.Control type="text" placeholder="Enter full name" onChange={(event) => this.setState({ name: event.target.value })} /></div>
              <br />
              <div className="login-card"><Form.Control type="email" placeholder="Enter email" onChange={(event) => this.setState({ email: event.target.value })} /></div>
              <br />
              <div className="login-card"><Form.Control type="password" placeholder="Password" onChange={(event) => this.setState({ password: event.target.value })} /></div>
              <br />
              <div className="login-card"> <Form.Control type="password" placeholder="Confirm Password" onChange={(event) => this.setState({ password2: event.target.value })} /></div>

              <Form.Group className="mt-4 d-inline-flex" controlId="formBasicCheckbox">
                <Form.Check onChange={(event) => this.setState({ isChecked: event.target.checked })} type="checkbox" label="" /><div className="terms">I agree to the <Button variant="link" onClick={() => { window.open("/terms-of-service" )}} >Terms of Condition</Button></div>
              </Form.Group>
              <br /> <br />
              <div className="login-card"> <Button style={{border:"none"}} onClick={() => this.signUpWithUsernamePassword()}>Sign Up</Button></div> <br /> <br />
              <Button variant="link" onClick={() => this.showSignInOptions()}>Already have an account? Sign In</Button><br />
              <Button variant="link" onClick={() => this.showSignInOptions()}>Back to Login options</Button>
            </div>
          </>
        )}

        {this.state.isMobileSignInVisible && (
          <>
            <h4>Login</h4>
            <div className="buttonDiv">
              <div className="login-card" style={{ display: 'flex', paddingLeft: '20px' }}>
                <PhoneInput
  
                  style={{marginLeft:'1.2rem'}}
                  placeholder="Enter phone number"
                  value={this.state.mobile}
                  onChange={mobile => this.setState({ mobile })} />
                {/* <Form.Control style={{ width: "22%" }} type="text" placeholder="Code" onChange={(event) => this.setState({ mobilecode: event.target.value })} /> */}
                {/* <Form.Control style={{ width: "76%" }} type="number" placeholder="Enter Mobile Number" onChange={(event) => this.setState({ mobile: event.target.value })} /> */}
              </div>
              <br />
              <div id="captcha-container"></div>
              <div className="login-card"><Button id="send-otp" onClick={() => this.signInWithMobile()}>Send OTP</Button></div>
              <br /> <br />
              <div className="login-card"> <Form.Control type="number" placeholder="Enter OTP" onChange={(event) => this.setState({ otp: event.target.value })} /></div>
              <br /> <br />
              <div className="login-card"> <Button onClick={() => this.validateMobileOtp()}>Sign In</Button> </div>




              <br /> <br />
              <Button variant="link" onClick={() => this.showSignInOptions()}>Back to Login options</Button>
            </div>
          </>

        )}

        {this.state.errorMessage && (<Alert variant="danger">{this.state.errorMessage}</Alert>)}
        {this.state.successMessage && (<Alert variant="success">{this.state.successMessage}</Alert>)}
      </>
    );
  }
}

export default Auth;
