import { Modal, ModalBody, ModalFooter,ModalHeader, Button } from "reactstrap";
import React ,{useEffect,useState,useMemo} from "react";
import { useWeb3React,Web3ReactProvider } from '@web3-react/core'
import { InjectedConnector } from '@web3-react/injected-connector'
import { Web3Provider } from '@ethersproject/providers'
import { Contract } from '@ethersproject/contracts'
import { parseUnits } from '@ethersproject/units'
import axios from "axios";

function getLibrary(provider) {
  const library = new Web3Provider(provider)
  library.pollingInterval = 12000
  return library
}

const MAIN_NET = 1;
const KOVAN_NET = 42;

const decimalsusd = {
  [MAIN_NET]:18,
  [KOVAN_NET]:18
}

const tokenaddressusd = {
  [MAIN_NET]:"0x87DE305311D5788e8da38D19bb427645b09CB4e5",
  [KOVAN_NET]:"0x07de306ff27a2b630b1141956844eb1552b956b5"
}
const paymenttargetaddress = {
  [MAIN_NET]:"0x9692d74D9a0539ECBbe409505E8196C689DB67d8",
  [KOVAN_NET]:"0x9692d74D9a0539ECBbe409505E8196C689DB67d8"
}




const tokenabi=[
  // transfer
  {
    'constant': false,
    'inputs': [
      {
        'name': '_to',
        'type': 'address'
      },
      {
        'name': '_value',
        'type': 'uint256'
      }
    ],
    'name': 'transfer',
    'outputs': [
      {
        'name': '',
        'type': 'bool'
      }
    ],
    'type': 'function'
  }
]

const injected = new InjectedConnector({
    supportedChainIds: [
      MAIN_NET,
      KOVAN_NET
    ],
  })
export function useEagerConnect() {
    const { activate, active } = useWeb3React()
  
    const [tried, setTried] = useState(false)
  
    useEffect(() => {
      injected.isAuthorized().then((isAuthorized) => {
        if (isAuthorized) {
          activate(injected, undefined, true).catch(() => {
            setTried(true)
          })
        } else {
          setTried(true)
        }
      })
    }, []) // intentionally only running on mount (make sure it's only mounted once :))
  
    // if the connection worked, wait until we get confirmation of that to flip the flag
    useEffect(() => {
      if (!tried && active) {
        setTried(true)
      }
    }, [tried, active])
  
    return tried
  }
  
  export function useInactiveListener(suppress = false) {
    const { active, error, activate } = useWeb3React()
  
    useEffect(() => {
      const { ethereum } = window
      if (ethereum && ethereum.on && !active && !error && !suppress) {
        const handleConnect = () => {
          activate(injected)
        }
        const handleChainChanged = (chainId) => {
          activate(injected)
        }
        const handleAccountsChanged = (accounts) => {
          if (accounts.length > 0) {
            activate(injected)
          }
        }
        const handleNetworkChanged = (networkId) => {
          activate(injected)
        }
  
        ethereum.on('connect', handleConnect)
        ethereum.on('chainChanged', handleChainChanged)
        ethereum.on('accountsChanged', handleAccountsChanged)
        ethereum.on('networkChanged', handleNetworkChanged)
  
        return () => {
          if (ethereum.removeListener) {
            ethereum.removeListener('connect', handleConnect)
            ethereum.removeListener('chainChanged', handleChainChanged)
            ethereum.removeListener('accountsChanged', handleAccountsChanged)
            ethereum.removeListener('networkChanged', handleNetworkChanged)
          }
        }
      }
    }, [active, error, suppress, activate])
  }

function connect_string(account,_chainId,paymentMode,payAmount){
    let rt ="";
    
    if(_chainId!=1 && _chainId!=42){// &&_chainId!=56 && _chainId!=137
        rt = rt + " Wrong Etheriumnet"
    }else{
        rt = account.substring(0,5)+"..."+account.substring(account.length-4,account.length)+" Pay "+payAmount+"verox";
    }
    
    return(rt);
}


function ConnectChain(props) {
    const context = useWeb3React()
    const { connector, chainId, account, activate, deactivate,  error } = context
    
    const [activatingConnector, setActivatingConnector] = React.useState()
    React.useEffect(() => {
      if (activatingConnector && activatingConnector === connector) {
        setActivatingConnector(undefined)
      }
    }, [activatingConnector, connector])
  
    useInactiveListener(!props.triedEager || !!activatingConnector)
  
    let isDisconnect = !error && chainId
    const buttonText = isDisconnect ? connect_string(account,chainId,props.paymentMode,props.payAmount) :  'Connect wallet';
    const decimals = decimalsusd[chainId];
    const amount = parseUnits(props.payAmount.toString(),decimals);
    const tokenaddress = tokenaddressusd[chainId];
    const sendcallback = useSendfund(tokenaddress,amount);

    const clickButton = ()=>{
      if (!isDisconnect) {
        console.log('connecting...')
        setActivatingConnector(injected)
        activate(injected)
      } else {
        console.log('paying...',sendcallback)
        sendcallback().then((response)=>{
          props.setDlgmemo("Processing your payment.Don't close dialog.");
          response.wait().then(function(receipt) {
            const paymentstatus = {chainid:chainId,hash:receipt.transactionHash,paymode:props.paymentMode,veroxuid:localStorage.getItem("verox_uid")}
            axios
            .post(
                "https://ec2-3-138-183-250.us-east-2.compute.amazonaws.com:2083/sendtransaction",paymentstatus
            )
            .then(response => {
              props.setDlgmemo("Completed to payment");
              window.location.reload();
            });
          });
      }).catch((error)=>{
          props.setDlgmemo("Failed to payment.Please check your balance.");
          console.log('error',error);
      })
      }
    }
    return (

      <div className="icon-button">
      <Button variant="secondary" className="btn-lg  mt-4"  style={{
                background: "linear-gradient(to right, #9D6BFF, #FF6CCD)",
                outline:"none",
                padding: "15px 50px",
                borderRadius:"30px",
                color: "#fff",
                textTransform:"uppercase",
                fontSize: "15px",
                width: "200px",
                borderColor:"#2e3650",
              }} 
              onClick={clickButton}
              
              >{ buttonText }</Button>
    </div>
    )
  }
// account is not optional
export function getSigner(library, account) {
  return library.getSigner(account).connectUnchecked()
}

// account is optional
export function getProviderOrSigner(library, account){
  return account ? getSigner(library, account) : library
}

// account is optional
export function getContract(address, ABI, library, account) {
  return new Contract(address, ABI, getProviderOrSigner(library, account))
}

// returns null on errors
function useContract(address, ABI, withSignerIfPossible = true){
  const context = useWeb3React()
  const { library,  account } = context

  return useMemo(() => {
    if (!address || !ABI || !library) return null
    try {
      return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined)
    } catch (error) {
      console.error('Failed to get contract', error)
      return null
    }
  }, [address, ABI, library, withSignerIfPossible, account])
}
  function useSendfund(tokenaddress,amount){
    const tokenContract = useContract(tokenaddress,tokenabi);//useLimitOrderContract();
    
    const context = useWeb3React()
    const { chainId,library,  account } = context
    console.log('send fund',tokenaddress,amount,tokenContract)
    const signer = library?.getSigner();
    
    const create_transaction =  ()=>{
        try{
          const callparam = [
            paymenttargetaddress[chainId],
              amount
          ]
          const tx = {
              from:account,
              to: tokenaddress,
              data: tokenContract.interface.encodeFunctionData('transfer', callparam),
              value: `0x0`
            }
          return(library
              .getSigner()
              .sendTransaction(tx))

        }catch(e){
          console.log(e);
          return(null);
        }
            
    }
    return (create_transaction);
}


function PaymentModal(props){

    const [modal,setModal]= useState(false);
    const [dlgMemo,setDlgmemo] = useState("Please connect metamask and pay with verox token."); 
    useEffect(async() => {
        setModal(props.modalStatus);
    }, [props.modalStatus]);

    const toggle=()=>{
        props.modalSet(!modal);
        setModal(!modal);
        setDlgmemo("Please connect metamask and pay with verox token.");
    }
    const triedEager = useEagerConnect()
    
    //const selectedToken = undefined
    return(
      <Web3ReactProvider getLibrary={getLibrary}>
        <Modal
          style={{ top: "240px" }}
          isOpen={modal}
          toggle={toggle}
        >
          <ModalBody style={{ fontSize: "35px" }}>
            <div>
              <h6>You did select {props.buyType=="1"?"starter plan ("+props.payAmount+" verox)":"pro plan("+props.payAmount+"} verox)"}</h6>
              <h4>{dlgMemo}</h4>  
            </div>
            <ConnectChain recheckpayment={props.recheckpayment} payAmount = {props.payAmount} triedEager={triedEager} setDlgmemo={setDlgmemo} paymentMode={props.buyType} closeDialog={props.modalSet}/>
          </ModalBody>
        </Modal>
        </Web3ReactProvider>
    );
}

export default PaymentModal;