import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";

import DOMPurify from "dompurify";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleDown,
  faAngleUp,
  faArrowUp,
} from "@fortawesome/free-solid-svg-icons";
import { Sparklines, SparklinesLine } from "react-sparklines";
import MakeFavourites from "./make-favourite";
import MakeAlerts from "./make-alert";
import { Pagination, PaginationItem } from "reactstrap";
import { withStyles } from "@material-ui/core";
import { NumberFormat } from "../../common/number-format";
import Loader from "../shared/loader";
import axios from "axios";

const useStyles = (theme: any) => ({
  root: {
    marginLeft: theme.spacing(3),
    "& > *": {
      marginTop: theme.spacing(2),
      padding: theme.spacing(1),
    },
  },
});

interface RecentSignalsProps {
  webSocket: any;
  checkLogin: () => void;
  classes: any;
  checkPayment: () => void;
}

const myHTML = `<p class="title-main"></p>
    				
<ul class="li-link">
<li>VeroxAI is on the lookout for the perfect assets for you to HODL.</li>
<li>Discover the projects that go beyond current hype.</li>
<li>Our algorithm intelligently assesses millions of end points to find the projects with excellent fundamentals, innovative underlying technologies, solid use cases, potential market adoption, high expected profit and more. 		</li>
<p class="desc-main coming-soon-data">Coming Soon</p> `;

const mySafeHTML = DOMPurify.sanitize(myHTML, {
  ALLOWED_TAGS: ["h1", "p", "span", "ul", "li"],
  ALLOWED_ATTR: ["style", "class"],
});

interface RecentSignalsState {
  recentSignalsData: any;
  recentSignalCoins: any[];
  isRecentSignalsDataLoading: boolean;
  marketData: any[];
}

interface RecentSignalsProps {
  webSocket: any;
  checkLogin: () => void;
}
// component for best long term investments
class RecentSignals extends React.Component<
  RecentSignalsProps,
  RecentSignalsState
> {
  constructor(props: RecentSignalsProps) {
    super(props);
    props.checkLogin();
    props.checkPayment();
    this.state = {
      recentSignalsData: null,
      recentSignalCoins: [],
      isRecentSignalsDataLoading: true,
      marketData: [],
    };
  }

  componentDidMount() {
    document.title = "Recent Signals | Verox";

    // axios
    //   .get(
    //     `https://api.veroxai.io/markets`
    //   )
    //   .then((response: any) => {
    //     console.log(response.data);
    //     this.setState({
    //       marketData: response.data.coins,
    //     });
    //   });
    axios
      .get(
        "https://api.veroxai.io/recent/signal/"
      )
      .then((response: any) => {
        console.log("check2", response.data);
        if (response.data && response.data.signals) {
          const coinsData:any = {}
          axios
            .get(
              `https://api.veroxai.io/markets`
            )
            .then((response: any) => {
              console.log(response.data);
              response.data.coins.map((coin: any) => {
                coinsData[coin.symbol.toLowerCase()] = {
                  id: coin.id,
                  name: coin.name,
                  image: coin.image,
                  current_price: coin.current_price,
                }
              })
            });
          this.setState({
            recentSignalCoins: response.data.signals,
            recentSignalsData: coinsData,
            isRecentSignalsDataLoading: false,
          });
        }
      });

    // this.props.webSocket.emit("getRecentSignals", {});
    // this.props.webSocket.on("recentSignals", (response: any) => {
    //   console.log("check2", response.data);
    //   if (response.data && response.data.details && response.data.signals) {
    //     this.setState({
    //       recentSignalCoins: response.data.details,
    //       recentSignalsData: response.data.signals,
    //       isRecentSignalsDataLoading: false,
    //     });
    //     this.props.webSocket.removeAllListeners("recentSignals"); // this is just one-time listener
    //   }
    // });
  }

  render() {
    let { classes } = this.props;
    console.log("check2 aa", this.state);

    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4 style={{ fontSize: "2.25rem" }}>Recent Signals</h4>
                  <p>Check recent signals</p>
                </div>
              </Container>
            </Navbar>

            {/* <Row className="terms-of-use">
              <Col xs={12} sm={12} xl={12}>
                <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
              </Col>
            </Row> */}

            <Row className="justify-content-md-center main-markets-table-head">
              <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                <h5 className="main-head-coin-d">Coin</h5>
              </Col>
              <Col xs={2} sm={2} xl={2} className="mb-3">
                <h5>Current Price</h5>
              </Col>
              <Col xs={2} sm={2} xl={2} className="mb-3">
                <h5>Signal</h5>
              </Col>
              <Col xs={2} sm={2} xl={2} className="mb-3">
                <h5>Signal Price</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Signal Date-Time</h5>
              </Col>
            </Row>

            <div
              // style={{ overflowY: "scroll", maxHeight: "81rem" }}
              className="main-table-cap"
              id="main-table-cap"
            >
              {this.state.isRecentSignalsDataLoading && <Loader />}

              {!this.state.isRecentSignalsDataLoading &&
                this.state.recentSignalCoins.length > 0 &&
                this.state.recentSignalCoins.map((coin: any, index: number) => {
                  let dateTime = coin.time.toString().split(/[T | .]/);
                  // console.log(
                  //   "recent signals",
                  //   coin.time.split(/[T | .]/)
                  // );
                  return (
                    <Row
                      key={this.state.recentSignalsData[coin.coin.toLowerCase()] && this.state.recentSignalsData[coin.coin].id}
                      className="justify-content-md-center"
                      onClick={() => {
                        window.location.hash =
                          "/coin/" + coin.coin + "/" + this.state.recentSignalsData[coin.coin.toLowerCase()] && this.state.recentSignalsData[coin.coin].id;
                      }}
                    >
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                        <table className="main-coin-name">
                          <tr>
                            <td>
                              <img src={this.state.recentSignalsData[coin.coin.toLowerCase()] && this.state.recentSignalsData[coin.coin].image} alt={this.state.recentSignalsData[coin.coin.toLowerCase()] && this.state.recentSignalsData[coin.coin].name} />
                            </td>
                            <td>
                              <h6>
                                {this.state.recentSignalsData[coin.coin.toLowerCase()] && this.state.recentSignalsData[coin.coin].name} <br /> <span>{coin.coin}</span>
                              </h6>{" "}
                            </td>
                          </tr>
                        </table>
                      </Col>
                      <Col xs={2} sm={2} xl={2} className="mb-3">
                        <p className="PriceData">
                          ${this.state.recentSignalsData[coin.coin.toLowerCase()] ? NumberFormat(this.state.recentSignalsData[coin.coin.toLowerCase()].current_price) : 0}
                        </p>
                      </Col>
                      <Col xs={2} sm={2} xl={2} className="mb-3">
                        {coin.text === "Buy" ? (
                          <p className="PriceData text-success">
                            {coin.text}
                          </p>
                        ) : (
                          <p className="PriceData text-danger">
                            {coin.text}
                          </p>
                        )}
                      </Col>
                      <Col xs={2} sm={2} xl={2} className="mb-3">
                        <p className="PriceData">
                          $
                          {coin.price}
                        </p>
                      </Col>
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                        <p className="PriceData">
                          {dateTime[0] + " " + dateTime[1]}
                        </p>
                      </Col>
                    </Row>
                  );
                })}

              {/*  TODO: fix this */}
              {/* {!this.state.isRecentSignalsDataLoading && this.state.recentSignalCoins.length > 0 && (
                <ScrollUpButton ContainerClassName="ScrollUpButton__Container" TransitionClassName="ScrollUpButton__Toggled">
                  <p> <FontAwesomeIcon icon={faAngleUp} /> Scroll To Top  </p>
                </ScrollUpButton> 
              )} */}

              {!this.state.isRecentSignalsDataLoading &&
                this.state.recentSignalCoins.length === 0 && (
                  <Row className="justify-content-md-center">
                    <Col xs={12} sm={12} xl={12}>
                      <div className="no-data">
                        <h6>No data found for markets.</h6>
                      </div>
                    </Col>
                  </Row>
                )}
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default withStyles(useStyles)(RecentSignals);
