import {
  Col,
  Container,
  Form,
  Navbar,
  Row,
  Button,
} from "@themesberg/react-bootstrap";

import DOMPurify from "dompurify";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import { Dropdown } from "react-bootstrap";
import VALUE_OF_TRADER_JSON from "../../common/value-of-trader";
import binanceImg from "../../assets/images/Binance.png";
import okexImg from "../../assets/images/Okex.png";
import ftxImg from "../../assets/images/Ftx.png";
import huobiImg from "../../assets/images/huobi.png";
import { FormControlLabel, Switch, Typography } from "@material-ui/core";

const myHTML = `
<p class="title-main">Automated Trading Bots Settings</p>
<ul class="li-link">
    <li>For trading on the available exchanges, you can add or delete key pairings. </li>  
    <li>False keys are absolutely prohibited and will result your account in being banned. </li>  </ul>
<p class="title-main">Automated Trading Bot Usage Instructions and VeroxAI V2 Performance Results</p>
<ul class="li-link">
    <li>Enter your Binance Account API key. </li>  
    <li>Enter your Binance Account Secret key.</li>  
    <li>Select the coin from the list of coins. 20 coins are supported as of now. More coins 
    will get added in the future.</li>  
    <li>Push the toggle to ON state. You will see the text Bot is running.</li>  
    <li>Click save. Only on saving, the bot will start running and execute orders on your 
    behalf.</li>  
    <li>Push the toggle to OFF state. You will see the text Bot is not running.
    </li>  
    <li>Click on save. Only on saving, the bot will stop running on exchange.</li>  
    <li>All the coins which the bot have bought, will remain in your account. User will 
    have to sell them manually or users can keep them as they wish.</li>  
    <li>Users can change coins and exchange keys only when the bot is in not running
    state.</li>  
    <li>VeroxAI V2 Algorithm Performance Testing Results: <a style="color:rgb(19,199,255)" href='https://drive.google.com/file/d/1qL6mhvUgsaLzQ8UZkqCELLQiYslwPaGC/view'>https://drive.google.com/file/d/1qL6mhvUgsaLzQ8UZkqCELLQiYslwPaGC/view</a></li>
</ul>
`;

const mySafeHTML = DOMPurify.sanitize(myHTML, {
  ALLOWED_TAGS: ["h1", "p", "span", "ul", "li", "a", "br"],
  ALLOWED_ATTR: ["style", "class", "href"],
});

interface TradingBotState {
  userDetails: any;
  key: any;
  secret: any;
  status: any;
  coinsList: any;
  selectedCoin: any;
}

interface TradingBotProps {
  webSocket: any;
  checkLogin: () => void;
  checkPayment: () => void;
}

// component for automated trading bot
class TradingBot extends React.Component<TradingBotProps, TradingBotState> {
  constructor(props: TradingBotProps) {
    super(props);
    props.checkLogin();
    props.checkPayment();
    this.state = {
      userDetails: null,
      key: "",
      secret: "",
      status: false,
      selectedCoin: null,
      coinsList: null,
    };
  }

  componentDidMount() {
    document.title = "Automated Trading Bot | Verox";

    this.setState({
      ...this.state,
      coinsList: VALUE_OF_TRADER_JSON,
    });

    this.props.webSocket.emit("getUserDetails", {
      userId: localStorage.getItem("verox_uid"),
    });
    this.props.webSocket.on("userDetails", (response: any) => {
      console.log("check2", response);
      if (response.data) {
        console.log(response.data, "toggle");
        this.setState({
          ...this.state,
          userDetails: response.data,
          key:
            response.data.binance &&
            response.data.binance.key &&
            response.data.binance.key,
          secret:
            response.data.binance &&
            response.data.binance.secret &&
            response.data.binance.secret,
          status:
            response.data.binance && response.data.binance.status
              ? response.data.binance.status
              : false,
          selectedCoin: response.data.binance &&
            response.data.binance.coin && {
              coinName: this.state.coinsList[response.data.binance.coin],
              coinId: response.data.binance.coin,
            },
        });
        this.props.webSocket.removeAllListeners("userDetails"); // this is just one-time listener
      }
    });
  }

  saveDetails = () => {
    this.props.webSocket.emit("pushUserDetials", {
      userId: this.state.userDetails.userId,
      binance: {
        key: this.state.key,
        secret: this.state.secret,
        coin: this.state.selectedCoin.coinId,
        status: this.state.status ? this.state.status : false,
      },
    });
    alert(`The Bot is now ${this.state.status ? "Running" : "Stopped"}`);
    this.props.webSocket.emit("getUserDetails", {
      userId: localStorage.getItem("verox_uid"),
    });
    this.props.webSocket.on("userDetails", (response: any) => {
      console.log("check2", response);
      if (response.data) {
        this.setState({
          userDetails: response.data,
          status:
            response.data.binance && response.data.binance.status
              ? response.data.binance.status
              : false,
          selectedCoin: {
            coinName: this.state.coinsList[response.data.binance.coin],
            coinId: response.data.binance.coin,
          },
        });
        this.props.webSocket.removeAllListeners("userDetails"); // this is just one-time listener
      }
    });
  };

  render() {
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-50">
                  <h4 style={{ fontSize: "2.25rem" }}>Automated Trading Bot</h4>
                  <p>
                    Never let your emotions influence your decision-making, and
                    never pass up an opportunity to buy or sell. That is made
                    possible by VeroxAI trading bots, which allow you to execute
                    trades without allowing emotions to get in the way. VeroxAI
                    automated trading bot is pre-programmed to execute precise
                    trades across multiple platforms at breakneck speed. Our
                    automated trading platform allows you to trade while you are
                    sleeping or away, ensuring that you do not miss out on
                    anything
                  </p>
                  <p className="text-danger">
                    Note: &nbsp; The amount of USDT which is available on users
                    spot account on binance will be used for automated trading
                    capped at 10,000 USDT per trade. This is a compounding bot,
                    so all the USDTs {"(<=10k$)"} which are available on spot
                    account will be used for buying and selling coins.
                  </p>
                </div>
              </Container>
            </Navbar>

            <Row className="terms-of-use">
              <Col xs={12} sm={12} xl={12}>
                <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
              </Col>
            </Row>
            <div className="trading-bot_page">
              <div className="bot-section d-flex flex-column p-5 mx-auto mt-5">
                <div className="d-flex mb-4 align-items-center justify-content-between">
                  <label className="bot-keys-label" htmlFor="">
                    Binance Keys
                  </label>
                  <img className="exchange-img" src={binanceImg} alt="" />
                </div>
                <div className="inputs_container mt-4">
                  <Form>
                    <Form.Group>
                      <Form.Control
                        className="bot-inputs mb-4"
                        type="text"
                        name=""
                        placeholder="Enter API Key"
                        autoComplete="off"
                        value={
                          this.state.userDetails &&
                          this.state.userDetails.binance &&
                          this.state.userDetails.binance.status
                            ? "************************"
                            : this.state.key
                        }
                        disabled={
                          this.state.userDetails &&
                          this.state.userDetails.binance &&
                          this.state.userDetails.binance.status
                        }
                        onChange={(e) => {
                          this.setState({
                            ...this.state,
                            key: e.target.value,
                          });
                        }}
                      />
                      <Form.Control
                        className="bot-inputs mb-4"
                        type="text"
                        name=""
                        value={
                          this.state.userDetails &&
                          this.state.userDetails.binance &&
                          this.state.userDetails.binance.status
                            ? "************************"
                            : this.state.secret
                        }
                        disabled={
                          this.state.userDetails &&
                          this.state.userDetails.binance &&
                          this.state.userDetails.binance.status
                        }
                        placeholder="Enter Secret Key"
                        autoComplete="off"
                        onChange={(e) => {
                          this.setState({
                            ...this.state,
                            secret: e.target.value,
                          });
                        }}
                      />
                      {/* Coins */}
                      <p className="">
                        <Dropdown className="">
                          <Dropdown.Toggle
                            disabled={
                              this.state.userDetails &&
                              this.state.userDetails.binance &&
                              this.state.userDetails.binance.status
                            }
                          >
                            {this.state.selectedCoin
                              ? this.state.selectedCoin.coinName
                              : "Select Coin"}
                          </Dropdown.Toggle>

                          <Dropdown.Menu
                            className="bot-menu"
                            style={{ maxHeight: "50rem", overflow: "scroll" }}
                          >
                            {Object.keys(VALUE_OF_TRADER_JSON).map(
                              (coin: any) => {
                                console.log("1234", coin);
                                return (
                                  <>
                                    <Dropdown.Item
                                      onClick={() => {
                                        this.setState({
                                          ...this.state,
                                          selectedCoin: {
                                            coinId: coin,
                                            coinName:
                                              this.state.coinsList[coin],
                                          },
                                        });
                                      }}
                                    >
                                      <i className="fa fa-heart"></i>
                                      &nbsp;&nbsp; {coin}
                                    </Dropdown.Item>
                                    <Dropdown.Divider />
                                  </>
                                );
                              }
                            )}
                          </Dropdown.Menu>
                        </Dropdown>
                      </p>
                    </Form.Group>
                  </Form>
                  <div className="d-flex align-items-center flex-column mt-4">
                    <FormControlLabel
                      className="text-white"
                      control={
                        <Switch
                          checked={this.state.status}
                          defaultChecked
                          onChange={(e) => {
                            this.setState({
                              ...this.state,
                              status: e.target.checked,
                            });
                          }}
                        />
                      }
                      label={
                        <Typography
                          style={{ fontSize: "20px", background: "none" }}
                        >
                          Bot is {this.state.status ? "" : "not"} running
                        </Typography>
                      }
                    />
                    <Button
                      variant="primary"
                      className="bot-form-btn mb-2"
                      onClick={() => {
                        this.saveDetails();
                      }}
                    >
                      Save
                    </Button>
                    {/* <Button
                      variant={`${this.state.status ? "danger" : "primary"}`}
                      className="bot-form-btn"
                      onClick={() => {
                        this.toggleTradingBot(!this.state.status);
                      }}
                    >
                      {this.state.status ? "Stop" : "Run"}
                    </Button> */}
                  </div>
                </div>
              </div>
              <li className="agree-terms mx-auto mt-2 mb-5">
                You agree to the terms by adding keys for binding
              </li>
              <div className="available-exchanges mb-5">
                <h5 className="available-exchanges_header">
                  Available Exchanges
                </h5>
                <div className="d-flex exchanges_container mb-4">
                  <img className="exchanges-img" src={binanceImg} alt="" />
                </div>
                <h5 className="available-exchanges_header">
                  Upcoming Exchanges
                </h5>
                <div className="d-flex exchanges_container flex-wrap">
                  <img className="exchanges-img" src={huobiImg} alt="" />
                  <img className="exchanges-img" src={ftxImg} alt="" />
                  <img className="exchanges-img" src={okexImg} alt="" />
                </div>
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default TradingBot;
