import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";

import Footer from "../shared/footer";
import Loader from "../shared/loader";
import React from "react";
import Sidebar from "../shared/sidebar";
import axios from "axios";

const dateFormat = require("dateformat");

interface NewsState {
    newsData: any[],
    isNewsLoading: boolean
}

interface NewsProps {
  webSocket: any,
  checkLogin: ()=>void
}

// listing of top picked coins
class News extends React.Component<NewsProps, NewsState> {

  constructor(props: NewsProps) {
    super(props);
    props.checkLogin();
    this.state = {
        newsData: [],
        isNewsLoading: true
    };
  }

  componentDidMount() {
    document.title = "News | Verox";
    axios.get('https://api.veroxai.io/news/?section=alltickers&items=50&token=oeptfszwnpuygimipibfxz4pxita7q0jmosubogt')
        .then((response: any) => {
            this.setState({newsData: response.data, isNewsLoading: false});
        });
  }

  render() {
    return (
        <>
        <Sidebar webSocket={this.props.webSocket}/>
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
        <Container fluid className="px-0">
          <div className="header-class justify-content-between w-100">
            <h4 style={{ fontSize: "2.25rem"}}>News!</h4>
            <p>Get all latest &amp; breaking news and updates in the crypto sphere</p>
          </div>
        </Container>
      </Navbar>

      <div className="news-data">

        <div className="news-table">
          <Row className="justify-content-md-center">
            {this.state.isNewsLoading && (
              <Loader />
            )}
            {!this.state.isNewsLoading && this.state.newsData.length > 0 && this.state.newsData.map((data,index) => {
                return(
                    <Col key={index} xs={12} sm={12} lg={6} xl={6} className="DetailsNEWS" onClick={() => window.open(data.link, "_blank")}>
                        <div className="row">
                        <div className="col-md-3 col-lg-2">
                            <img src={data.imgURL} alt={data.title} />
                        </div>
                        <div className="col-md-9 col-lg-10">
                            <div className="news-content">
                            <p className="newsDate">{dateFormat(data.feedDate, "ddd, mmmm dS, yyyy")}</p>
                            <p className="newsTitle" onClick={() => window.location.href = data.link}>{data.title}</p>
                            <p className="newsdesc">{data.description}</p>
                            <p className="newsAuthor">{data.source}</p>
                            </div>
                        </div>
                        </div>
                    </Col>
                )
            })}
            {!this.state.isNewsLoading && this.state.newsData.length === 0 && (
              <Col xs={12} sm={12} xl={12}>
                <div className="row">
                  <div className="col-md-12">
                    No news found.
                  </div>
                </div>
              </Col> 
            )}
          </Row>
        
        </div>
      </div>
          </div>
          <Footer/>
        </div>
        </>
    );
  }
}

export default News;
