import React from "react";
import { Col, Row, Navbar, Container} from '@themesberg/react-bootstrap';
import DOMPurify from "dompurify";
import Sidebar from "../../shared/sidebar";
import Footer from "../../shared/footer";

class CookiePolicy extends React.Component<{webSocket: any}, {}> {
  render(): any {        
    document.title = `Cookie Policy | Verox`; 
    const myHTML = `<p class="title-main"></p>
                    <p class="title-main">Definitions and key terms</p>
                    <p class="desc-main">To help explain things as clearly as possible in this Cookie Policy, every time any of these terms are referenced, are strictly defined as:</p>
                    <p class="desc-main">Cookie:small amount of data generated by a website and saved by your web browser. It is used to identify your browser, provide analytics, remember information about you such as your language preference or login information.</p>
                    <p class="desc-main">Company: when this policy mentions Company, we, us, or our, it refers to Limitless Vision Limited,  that is responsible for your information under this Privacy Policy.</p>
                    <p class="desc-main">Device:any internet connected device such as a phone, tablet, computer or any other device that can be used to visit VeroxAI and use the services.</p>
                    <p class="desc-main">Personal Data: any information that directly, indirectly, or in connection with other information  including a personal identification number  allows for the identification or identifiability of a natural person.</p>
                    <p class="desc-main">Service: refers to the service provided by VeroxAI as described in the relative terms (if available) and on this platform.</p>
                    <p class="desc-main">Third-party service:refers to advertisers, contest sponsors, promotional and marketing partners, and others who provide our content or whose products or services we think may interest you.</p>
                    <p class="desc-main">Website: VeroxAI."s" site, which can be accessed via this URL: www.veroxai.io</p>
                    <p class="desc-main">You: a person or entity that is registered with VeroxAI to use the Services.</p>  
                    <p class="title-main">Introduction</p>
                    <p class="desc-main">This Cookie Policy explains how VeroxAI and its affiliates (collectively "VeroxAI", "we", "us", and "ours"), use cookies and similar technologies to recognize you when you visit our website/app, including without limitation www.veroxai.io and any related URLs, mobile or localized versions and related domains / sub-domains ("Websites"). It explains what these technologies are and why we use them, as well as the choices for how to control them.</p>
                    <p class="title-main">What is a cookie?</p>
                    <p class="desc-main">A cookie is a small text file that is stored on your computer or other internet connected device in order to identify your browser, provide analytics, remember information about you such as your language preference or login information. They're completely safe and can't be used to run programs or deliver viruses to your device.</p>
                    <p class="title-main">Why do we use cookies?</p>
                    <p class="desc-main">We use first party and/or third party cookies on our website/app for various purposes such as:</p>
                    <ul class="li-link">
                    <li>To facilitate the operation and functionality of our website/app;</li>
                    <li>To improve your experience of our website/app and make navigating around them quicker and easier;</li>
                    <li>To allow us to make a bespoke user experience for you and for us to understand what is useful or of interest to you;</li>
                    <li>To analyze how our website/app is used and how best we can customize it;</li>
                    <li>To identify future prospects and personalize marketing and sales interactions with it;</li>
                    <li>To facilitate the tailoring of online advertising to your interests.</li>
                    </ul>
                    <p class="title-main">What type of cookies does VeroxAI use?</p>
                    <p class="desc-main">Cookies can either be session cookies or persistent cookies. A session cookie expires automatically when you close your browser. A persistent cookie will remain until it expires or you delete your cookies. Expiration dates are set in the cookies themselves; some may expire after a few minutes while others may expire after multiple years. Cookies placed by the website youre visiting are called first party cookies.</p>
                    <p class="desc-main">Strictly Necessary cookies are necessary for our website/app to function and cannot be switched off in our systems. They are essential in order to enable you to navigate around the website/app and use its features. If you remove or disable these cookies, we cannot guarantee that you will be able to use our website/app.</p>
                    <p class="title-main">We use the following types of cookies in our website/app:</p>
                    <p class="title-main">Errors and Omissions Disclaimer</p>
                    <p class="desc-main">VeroxAI is not responsible for any content, code or any other imprecision.</p>
                    <p class="desc-main">VeroxAI does not provide warranties or guarantees.</p>
                    <p class="desc-main">In no event shall VeroxAI be liable for any special, direct, indirect, consequential, or incidental damages or any damages whatsoever, whether in an action of contract, negligence or other tort, arising out of or in connection with the use of the Service or the contents of the Service. VeroxAI reserves the right to make additions, deletions, or modifications to the contents on the Service at any time without prior notice.</p>
                    <p class="title-main">General Disclaimer</p>
                    <p class="desc-main">The VeroxAI Service and its contents are provided "as is" and "as available" without any warranty or representations of any kind, whether express or implied. VeroxAI is a distributor and not a publisher of the content supplied by third parties; as such, VeroxAI exercises no editorial control over such content and makes no warranty or representation as to the accuracy, reliability or currency of any information, content, service or merchandise provided through or accessible via the VeroxAI Service. Without limiting the foregoing, VeroxAI specifically disclaims all warranties and representations in any content transmitted on or in connection with the VeroxAI Service or on sites that may appear as links on the VeroxAI Service, or in the products provided as a part of, or otherwise in connection with, the VeroxAI Service, including without limitation any warranties of merchantability, fitness for a particular purpose or non-infringement of third party rights. No oral advice or written information given by VeroxAI or any of its affiliates, employees, officers, directors, agents, or the like will create a warranty. Price and availability information is subject to change without notice. Without limiting the foregoing, VeroxAI does not warrant that the VeroxAI Service will be uninterrupted, uncorrupted, timely, or error-free.</p>
                    <p class="title-main">Essential Cookies</p>
                    <p class="desc-main">We use essential cookies to make our website/app work. These cookies are strictly necessary to enable core functionality such as security, network management, your cookie preferences and accessibility. Without them you wouldn't be able to use basic services. You may disable these by changing your browser settings, but this may affect how the Websites function.</p>
                    <p class="title-main">Performance and Functionality Cookies</p>
                    <p class="desc-main">These cookies are used to enhance the performance and functionality of our website/app but are non-essential to their use. However, without these cookies, certain functionality like videos may become unavailable or you would be required to enter your login details every time you visit the website/app as we would not be able to remember that you had logged in previously.</p>
                    <p class="title-main">Marketing Cookies</p>
                    <p class="desc-main">These account-based marketing cookies enable us to identify future prospects and personalize sales and marketing interactions with them.</p>
                    <p class="title-main">Analytics and Customization Cookies</p>
                    <p class="desc-main">These cookies collect information that is used to help us understand how our website/app is being used or how effective our marketing campaigns are, or to help us customize our website/app for you.</p>
                    <p class="desc-main">We use cookies served by Google Analytics to collect limited data directly from end-user browsers to enable us to better understand your use of our website/app. Further information on how Google collects and uses this data can be found at:https://www.google.com/policies/privacy/partners/. You can opt-out of all Google supported analytics on our Websites by visiting:https://tools.google.com/dlpage/gaoptout.</p>
                    <p class="title-main">Advertising Cookies</p>
                    <p class="desc-main">These cookies collect information over time about your online activity on the website/app and other online services to make online advertisements more relevant and effective to you. This is known as interest-based advertising. They also perform functions like preventing the same ad from continuously reappearing and ensuring that ads are properly displayed for advertisers. Without cookies, its really hard for an advertiser to reach its audience, or to know how many ads were shown and how many clicks they received.</p>
                    <p class="title-main">Social Media Cookies</p>
                    <p class="desc-main">These cookies are used when you share information using a social media sharing button or like button on our Site or you link your account or engage with our content on or through a social networking website such as Facebook, Twitter or Google+. The social network will record that you have done this. These cookies may also include certain code that has been placed on the platform to help track conversions from ads, optimize ads based on collected data, build targeted audiences for future ads, and remarket to qualified users who have already taken certain action on the platform.</p>
                    <p class="title-main">Third Party Cookies</p>
                    <p class="desc-main">Some cookies that have been set on our website/app are not set on a first party basis by VeroxAI. The Websites can be embedded with content from third parties to serve advertising. These third party service providers may set their own cookies on your web browser. Third party service providers control many of the performance and functionality, advertising, marketing and analytics cookies described above. We do not control the use of these third party cookies as cookies can only be accessed by the third party that originally set them.</p>
                    <p class="title-main">How you can manage cookies?</p>
                    <p class="desc-main">Most browsers allow you to control cookies through their 'settings' preferences. However, if you limit the ability of websites to set cookies, you may worsen your overall user experience, since it will no longer be personalized to you. It may also stop you from saving customized settings like login information. Browser manufacturers provide help pages relating to cookie management in their products.</p>
                    <p class="desc-main">Browser manufacturers provide help pages relating to cookie management in their products. Please see below for more information.</p>
                    <ul class="li-link">
                    <li>Google Chrome</li>
                    <li>Internet Explorer</li>
                    <li>Mozilla Firefox</li>
                    <li>Safari (Desktop)</li>
                    <li>Safari (Mobile)</li>
                    <li>Android Browser</li>
                    <li>Opera</li>
                    <li>Opera Mobile</li>
                    </ul>
                    <p class="title-main">Blocking and disabling cookies and similar technologies</p>
                    <p class="desc-main">Wherever you're located you may also set your browser to block cookies and similar technologies, but this action may block our essential cookies and prevent our website from functioning properly, and you may not be able to fully utilize all of its features and services. You should also be aware that you may also lose some saved information (e.g. saved login details, site preferences) if you block cookies on your browser. Different browsers make different controls available to you. Disabling a cookie or category of cookie does not delete the cookie from your browser, you will need to do this yourself from within your browser, you should visit your browser's help menu for more information.</p>
                    <p class="title-main">Changes To Our Cookie Policy</p>
                    <p class="desc-main">We may change our Service and policies, and we may need to make changes to this Cookie Policy so that they accurately reflect our Service and policies. Unless otherwise required by law, we will notify you (for example, through our Service) before we make changes to this Cookie Policy and give you an opportunity to review them before they go into effect. Then, if you continue to use the Service, you will be bound by the updated Cookie Policy. If you do not want to agree to this or any updated Cookie Policy, you can delete your account.</p>
                    <p class="title-main">Your Consent</p>
                    <p class="desc-main">By using our website/app, registering an account, or making a purchase, you hereby consent to our Cookie Policy and agree to its terms.</p>
                    <p class="title-main">Contact Us</p>
                    <p class="desc-main">Don't hesitate to contact us if you have any questions regarding our Cookie Policy.</p>
                    <p class="desc-main"> - Via Email:  team@veroxai.com</p>`;
    const mySafeHTML = DOMPurify.sanitize(myHTML, {
      ALLOWED_TAGS: ["h1", "p", "span", "ul", "li"],
      ALLOWED_ATTR: ["style","class"],
    });
    return (
      <>

<Sidebar webSocket={this.props.webSocket}/>
          <div className="app-container-with-sidebar">
            <div className="component">
        <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4>Cookie Policy</h4>
            </div>
          </Container>
        </Navbar>

        <div className="updated-on">
          <div className="header-class justify-content-between w-100">
            <p>Updated on 2021-05-23</p>
          </div>
        </div>

        <Row className="terms-of-use">
          <Col xs={12} sm={12} xl={12}>
            <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
          </Col>
          
        </Row>
          </div>
          <Footer/>
        </div>
      </>
    );
  }
}

export default CookiePolicy;