import {
  Alert,
  Badge,
  Button,
  Dropdown,
  DropdownButton,
  Image,
  Nav,
  Navbar,
} from "@themesberg/react-bootstrap";

import { CSSTransition } from "react-transition-group";
import { Link } from "react-router-dom";
import LogoWebsite from "../../assets/images/websitelogo.png";
import QR from "../../assets/images/qr.png";
import React from "react";
import { Routes } from "../../common/routes";
import SimpleBar from "simplebar-react";
import firebase from "firebase";
import { Switch } from "@material-ui/core";
import ThemeContext, { ThemeProvider } from "../../theme/themeContext";

interface SidebarState {
  mobileSidebar: boolean;
  dark: boolean;
}

interface SidebarProps {
  webSocket: any;
}

class Sidebar extends React.Component<SidebarProps, SidebarState> {
  constructor(props: SidebarProps) {
    super(props);
    this.state = {
      mobileSidebar: false,
      dark: false,
    };
  }

  logout() {
    firebase
      .auth()
      .signOut()
      .then(function () {
        localStorage.removeItem("verox_uid");
        localStorage.removeItem("verox_user_display_name");
        localStorage.removeItem("verox_email");
        localStorage.removeItem("verox_phone");
        localStorage.removeItem("verox_signin");
        window.location.hash = "/markets";
        window.location.reload();
      })
      .catch(function (error) {
        window.location.reload();
      });
  }

  sidebar = () => {
    const navLink = document.querySelectorAll(".sidebar .nav-item .nav-link");
    const navLinkA = document.querySelectorAll(".sidebar .nav-item.active .nav-link");
    const navItemA = document.querySelectorAll(".sidebar .nav-item.active::before");
    const sidebarFa = document.querySelectorAll(".sidebar .fa");
    const sidebarFaA = document.querySelectorAll(".sidebar .nav-item.active > .nav-link .sidebar-icon .fa");
    const sidebarFas = document.querySelectorAll(".sidebar .fas");
    const sidebarFasA = document.querySelectorAll(".sidebar .nav-item.active > .nav-link .sidebar-icon .fas");
    const sidebarText = document.querySelectorAll(
      ".sidebar .nav-link .sidebar-text"
    );
    const sidebarTextA = document.querySelectorAll(
      ".sidebar .nav-item.active > .nav-link .sidebar-text"
    );

    if (this.context.dark) {
      Array.from(navLink as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.webkitTextFillColor = "inherit";
      });
      Array.from(sidebarFa as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.webkitTextFillColor = "inherit";
      });
      Array.from(sidebarFaA as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        // link.style.webkitTextFillColor = "inherit";
        link.style.setProperty("color","#13C7FF","important");
      });
      Array.from(sidebarFas as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.webkitTextFillColor = "inherit";
      });
      Array.from(sidebarFasA as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.setProperty("color","#13C7FF","important");
      });
      Array.from(sidebarText as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.color = "#6979c5"
        link.style.webkitTextFillColor = "inherit";
      });
      Array.from(sidebarTextA as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        // link.style.background = "none";
        link.style.setProperty("color","#13C7FF","important");
      });
      Array.from(navLinkA as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        link.style.background = "#2e3650";
        // link.style.webkitTextFillColor = "inherit";
      });
      Array.from(navItemA as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "content-box";
        link.style.setProperty("background","#05B4FF","important");
        // link.style.webkitTextFillColor = "inherit";
      });
    } else {
      Array.from(navLink as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "text";
        // link.style.background = "linear-gradient(to right, #9d6bff, #ff6ccd)";
        link.style.webkitTextFillColor = "transparent";
      });
      Array.from(sidebarFa as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "text";
        // link.style.background = "linear-gradient(to right, #9d6bff, #ff6ccd)";
        link.style.webkitTextFillColor = "transparent";
      });
      Array.from(sidebarFas as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "text";
        // link.style.background = "linear-gradient(to right, #9d6bff, #ff6ccd)";
        link.style.webkitTextFillColor = "transparent";
      });
      Array.from(sidebarText as NodeListOf<HTMLElement>).map((link) => {
        // link.style.backgroundClip = "text";
        // link.style.background = "linear-gradient(to right, #9d6bff, #ff6ccd)";
        link.style.webkitTextFillColor = "transparent";
      });
    }
  };

  componentDidUpdate(prevProps: any, prevState: any) {
    console.log("cccccc",this.context)

    // if (prevState.dark !== this.context.dark) {
      const root = document.documentElement;
      let variables = getComputedStyle(root);
      this.sidebar();
      if (this.context.dark) {
        root?.style.setProperty("--bs-body-bg", "#040a16");
        root?.style.setProperty("--bs-primary-rgb", "5,11,35");
        root?.style.setProperty("--bs-dark-rgb", "5,11,35");
        root?.style.setProperty("--bs-dark", "#050b23");
        root?.style.setProperty("--footer-bg", "#030c29");
        root?.style.setProperty(
          "--text-gradient",
          "linear-gradient(to right, #1f0052, #510030)"
        );
        root?.style.setProperty("--theme-text-clip", "inherit");
      } else {
        root?.style.setProperty(
          "--bs-body-bg",
          variables.getPropertyValue("--bs-soft")
        );
        root?.style.setProperty("--bs-primary-rgb", "38,45,68");
        root?.style.setProperty("--bs-dark-rgb", "38,45,68");
        root?.style.setProperty("--bs-dark", "#262D44");
        root?.style.setProperty("--footer-bg", "#323b58");
        root?.style.setProperty(
          "--text-gradient",
          "linear-gradient(to right, #9d6bff, #ff6ccd)"
        );
        root?.style.setProperty("--theme-text-clip", "text");
      }
  }

  render() {
    return (
      <>
        <Navbar
          expand={false}
          collapseOnSelect
          variant="dark"
          className={`navbar-theme-primary px-4 main-navbar-head navbar-mobile ${
            this.context.dark ? "bg-dark" : ""
          }`}
        >
          <i
            className="fa fa-bars fa-2x"
            onClick={() =>
              this.setState({
                ...this.state,
                mobileSidebar: !this.state.mobileSidebar,
              })
            }
          ></i>
          <Navbar.Brand className="me-lg-5" as={Link} to={Routes.Markets.path}>
            <Image src={LogoWebsite} className="navbar-brand-light" /> - BETA
          </Navbar.Brand>
          <i className="fa fa-cog fa-2x"></i>
          <input type="checkbox" className="settings-toggler" name="" id="" />
          <div className="user-info">
            <p className="user-name">
              <a
                href="https://VeroxDex.io"
                target="_blank"
                className="text-decoration-none text-white"
              >
                VeroxDex
              </a>
              {/* <Dropdown >
                <Dropdown.Toggle>
                Download App
                </Dropdown.Toggle>
              
                

                <Dropdown.Menu className="downloads-menu">
                  <Image src={QR} className="qr-code" />
                  &nbsp;&nbsp;&nbsp;&nbsp;scan to download app
                </Dropdown.Menu>
              </Dropdown> */}
            </p>
            {!localStorage.getItem("verox_uid") && (
              <>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              </>
            )}

            {localStorage.getItem("verox_uid") && (
              <>
                <p className="user-name">
                  <div
                    onClick={() => {
                      window.location.hash = "/paymentplan";
                    }}
                  >
                    <Dropdown>
                      <Dropdown.Toggle id="dropdown-basic">
                        Membership
                      </Dropdown.Toggle>
                    </Dropdown>
                  </div>
                </p>
                <p className="user-name">
                  <div
                    onClick={() => {
                      window.location.hash = "/alerts";
                    }}
                  >
                    <Dropdown>
                      <Dropdown.Toggle id="dropdown-basic">
                        Alerts
                      </Dropdown.Toggle>

                      {/* <Dropdown.Menu className="alerts-menu"> */}
                      {/* <Alert>
                          This is a sample alert.
                        </Alert>
                        <Alert>
                          This is a sample alert.
                        </Alert>
                        <Alert>
                          This is a sample alert.
                        </Alert> */}
                      {/* </Dropdown.Menu> */}
                    </Dropdown>
                  </div>
                </p>

                <p className="user-name">
                  <Dropdown>
                    <Dropdown.Toggle id="dropdown-basic">
                      {localStorage.getItem("verox_user_display_name")}
                    </Dropdown.Toggle>

                    <Dropdown.Menu className="user-menu">
                      <Dropdown.Item
                        onClick={() => (window.location.hash = "/favourites")}
                      >
                        <i className="fa fa-heart"></i>&nbsp;&nbsp; Favourites
                      </Dropdown.Item>
                      <Dropdown.Divider />
                      <Dropdown.Item
                        onClick={() => (window.location.hash = "/alerts")}
                      >
                        <i className="fa fa-bell"></i>&nbsp;&nbsp; Alerts
                      </Dropdown.Item>
                      <Dropdown.Divider />
                      <Dropdown.Item
                        onClick={() =>
                          (window.location.hash = "/notifications")
                        }
                      >
                        <i className="fa fa-envelope"></i>&nbsp;&nbsp;
                        Notifications
                      </Dropdown.Item>
                      <Dropdown.Divider />
                      <Dropdown.Item
                        onClick={() => (window.location.hash = "/settings")}
                      >
                        <i className="fa fa-cog"></i>&nbsp;&nbsp; Settings
                      </Dropdown.Item>
                      <Dropdown.Divider />
                      <Dropdown.Item onClick={() => this.logout()}>
                        <i className="fa fa-sign-out-alt"></i>&nbsp;&nbsp;
                        Logout
                      </Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </p>
              </>
            )}
            <span
              className="d-flex align-items-center"
            >
              <i className={`fa fa-sun fa-2x ${!this.context.dark?"text-warning":"text-gray"}`}></i>
              <Switch
                          checked={this.context.dark}
                          onChange={(e) => {
                            this.context.changeTheme(e.target.checked)

                          }}
                        />
              <i className={`fa fa-moon fa-2x ${this.context.dark?"text-warning":"text-gray"}`}></i>
            </span>
          </div>
        </Navbar>
        <CSSTransition timeout={300} classNames="sidebar-transition">
          <SimpleBar
            className={` sidebar d-md-block bg-primary text-white ${
              this.state.mobileSidebar
                ? "sidebar-mobile"
                : "sidebar-mobile-closed"
            }`}
          >
            <div className="sidebar-inner px-4 pt-3">
              {!localStorage.getItem("verox_uid") && (
                <Nav className="flex-column pt-3 pt-md-0">
                  <NavItem
                    title="Markets"
                    link={Routes.Markets.path}
                    icon="fa fa-search-dollar"
                  />

                  <NavItem
                    title="Yield Farming"
                    link={Routes.YieldFarming.path}
                    icon="fa fa-bullseye"
                  />

                  <NavItem
                    title="Score Analyzer"
                    link={Routes.ScoreAnalyser.path}
                    icon="fa fa-tachometer-alt"
                  />

                  <NavItem
                    title="Admin Calls"
                    link={Routes.AdminCalls.path}
                    icon="fa fa-users"
                  />

                  <NavItem
                    title="News"
                    link={Routes.News.path}
                    icon="fa fa-rss"
                  />

                  <NavItem
                    title="Best Long Term Crypto Investments"
                    link={Routes.LongTermCrypto.path}
                    icon="fa fa-hand-holding-usd"
                  />

                  <NavItem
                    title="Automated Trading Bot"
                    link={Routes.AutomatedTrade.path}
                    icon="fas fa-robot"
                  />

                  <NavItem
                    title="FAQ"
                    link={Routes.FAQ.path}
                    icon="fa fa-question-circle"
                  />
                </Nav>
              )}

              {localStorage.getItem("verox_uid") && (
                <Nav className="flex-column pt-3 pt-md-0">
                  <NavItem
                    title="Markets"
                    link={Routes.Markets.path}
                    icon="fa fa-search-dollar"
                  />

                  <NavItem
                    title="Dashboard"
                    link={Routes.Dashboard.path}
                    icon="fa fa-poll"
                  />

                  <NavItem
                    title="Top Picks"
                    link={Routes.TopPicks.path}
                    icon="fa fa-star"
                  />

                  <NavItem
                    title="Most Mentioned"
                    link={Routes.MostMentioned.path}
                    icon="fa fa-fire-alt"
                  />

                  <NavItem
                    title="Biggest Volume Changes"
                    link={Routes.BiggestVolumeChanges.path}
                    icon="fa fa-reply"
                  />

                  <NavItem
                    title="Favourites"
                    link={Routes.MyFavourites.path}
                    icon="fa fa-star"
                  />

                  <NavItem
                    title="Alerts"
                    link={Routes.MyAlerts.path}
                    icon="fa fa-bell"
                  />

                  <NavItem
                    title="Passive Income"
                    link={Routes.YieldFarming.path}
                    icon="fa fa-bullseye"
                  />

                  <NavItem
                    title="Score Analyzer"
                    link={Routes.ScoreAnalyser.path}
                    icon="fa fa-tachometer-alt"
                  />

                  <NavItem
                    title="Admin Calls"
                    link={Routes.AdminCalls.path}
                    icon="fa fa-users"
                  />

                  <NavItem
                    title="News"
                    link={Routes.News.path}
                    icon="fa fa-rss"
                  />

                  <NavItem
                    title="Best Long Term Crypto Investments"
                    link={Routes.LongTermCrypto.path}
                    icon="fa fa-hand-holding-usd"
                  />

                  <NavItem
                    title="Automated Trading Bot"
                    link={Routes.AutomatedTrade.path}
                    icon="fas fa-robot"
                  />

                  <NavItem
                    title="Recent Signals"
                    link={Routes.RecentSignals.path}
                    icon="fa fa-signal"
                  />

                  <NavItem
                    title="FAQ"
                    link={Routes.FAQ.path}
                    icon="fa fa-question-circle"
                  />
                </Nav>
              )}
            </div>
          </SimpleBar>
        </CSSTransition>
      </>
    );
  }
}

Sidebar.contextType = ThemeContext

function NavItem(props: any) {
  const {
    title,
    link,
    target,
    icon,
    iconOther,
    image,
    badgeText,
    badgeBg = "secondary",
    badgeColor = "primary",
  } = props;
  const classNames = badgeText
    ? "d-flex justify-content-start align-items-center justify-content-between"
    : "";
  const navItemClassName =
    window.location.href.indexOf(link) !== -1 ? "active" : "";
  return (
    <>
      <Nav.Item className={navItemClassName}>
        <Nav.Link as={Link} to={link} target={target} className={classNames}>
          <span>
            {icon ? (
              <span className="sidebar-icon">
                <i className={icon}></i>{" "}
              </span>
            ) : null}
            {iconOther ? (
              <span className="sidebar-icon">
                <i className="fa fa-search"></i>{" "}
              </span>
            ) : null}
            {image ? (
              <Image
                src={image}
                width={20}
                height={20}
                className="sidebar-icon svg-icon"
              />
            ) : null}

            <span className="sidebar-text">{title}</span>
          </span>
          {badgeText ? (
            <Badge
              pill
              bg={badgeBg}
              text={badgeColor}
              className="badge-md notification-count ms-2"
            >
              {badgeText}
            </Badge>
          ) : null}
        </Nav.Link>
      </Nav.Item>
    </>
  );
}

export default Sidebar;
