import { Col, Container, Image, Modal, Nav, Navbar, Row, Tab } from "@themesberg/react-bootstrap";
import { faAngleUp, faLongArrowAltLeft } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Footer from "../shared/footer";
import { NumberFormat } from "../../common/number-format";
import Progress from "../shared/Progress";
import React from "react";
import Sidebar from "../shared/sidebar";

import getPathParam from "../../common/get-path-params";

interface ScoreAnalyserDetailState {
    coinid: string,
    coinname: string,
    scoreAnalyserData: any,
    isScoreAnalyserDataLoading: boolean,
    
}

interface ScoreAnalyserDetailProps {
    checkLogin: () => void,
    webSocket: any,
    checkPayment:()=>void
}


// listing of recommended coins using yield farming algorithm
class ScoreAnalyserDetail extends React.Component<ScoreAnalyserDetailProps, ScoreAnalyserDetailState> {
    constructor(props: ScoreAnalyserDetailProps) {
        super(props);
        props.checkLogin();
        props.checkPayment();
        this.state = {
            coinid: "",
            coinname: "",
            scoreAnalyserData: {},
            isScoreAnalyserDataLoading: true,
            
        };
    }

    componentDidMount() {
        document.title = "Score Details | Verox";
        const coinid: string = getPathParam("score-analyser-detail/");
        const coinname: string = getPathParam(`score-analyser-detail/${coinid}/`);
        this.props.webSocket.emit("getAllTokenScore", {});
        this.props.webSocket.on("tokenScore", (response: any) => {
            this.setState({coinid, coinname, scoreAnalyserData: response.data[coinid.toLocaleUpperCase()], isScoreAnalyserDataLoading: false});
            this.props.webSocket.removeAllListeners("tokenScore");  // this is just one-time listener
        });


    }

    

    render() {
        return (
            <>
            <Sidebar webSocket={this.props.webSocket}/>
            <div className="app-container-with-sidebar">
              <div className="component">
                <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
                    <Container fluid className="px-0">
                        <div className="header-class justify-content-between w-100">
                            <p className="back-arrow" onClick={() => window.history.back()}><FontAwesomeIcon icon={faLongArrowAltLeft} /> Back</p>
                        </div>
                        <div className="justify-content-between w-100 search-form-class">
                            <div className="align-items-center">

                            </div>
                        </div>
                    </Container>
                </Navbar>
                <br/><br/>
                <div className="coinData">
                    <Row>
                        <Col xs={6} sm={6} xl={2}>
                            <div className="coin-name">
                                <div className="main-coin-name">
                                    {/* <img src={"/assets/images/" + coinname.toLocaleLowerCase() + ".png"} alt={this.state.coinname} /> */}
                                    <h6>{this.state.coinname} <br/> <span>{this.state.coinid}</span></h6>
                                </div>
                            </div>
                        </Col>
                        <Col xs={6} sm={6} xl={2} className="border-right-class">
                            <div className="coin-price">
                                <p>Price</p>
                                <h3 className="mb-1">${NumberFormat(this.state.scoreAnalyserData.price_usd)} <FontAwesomeIcon icon={faAngleUp} className={`text-success me-1`} />
                                    <span className={`text-success fw-bold`}>
                                        0.00%
                                    </span></h3>
                            </div>
                        </Col>
                        <Col xs={6} sm={6} xl={2} className="border-right-class">
                            <div className="coin-price">
                                <p>Market Cap</p>
                                <h3 className="mb-1">${NumberFormat(this.state.scoreAnalyserData.market_cap_usd)}</h3>
                            </div>
                        </Col>
                        <Col xs={6} sm={6} xl={2}>
                            <div className="coin-price">
                                <p>Volume</p>
                                <h3 className="mb-1">{NumberFormat(this.state.scoreAnalyserData.volume_usd)}</h3>
                            </div>
                        </Col>

                        <Col xs={12} sm={12} xl={4}>
                            <div className="coin-score">
                                <p className="agg-score">agg.score <span>{NumberFormat(this.state.scoreAnalyserData.overall_score * 10)}/100</span></p>
                                <Progress variant="secondary" size="lg" value={this.state.scoreAnalyserData.overall_score * 10} />
                            </div>
                        </Col>
                    </Row>
                </div>
                <br/><br/>
                

                <div className="score-analyser-tab">
                    <Tab.Container defaultActiveKey="home">
                        <Row>
                            <Col lg={12}>
                                <Nav className="nav-tabs">
                                    <Nav.Item>
                                        <Nav.Link eventKey="home">
                                            Fundamental
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="technical">
                                            Technical
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="news">
                                            News
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="twitter">
                                            Twitter
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="reddit">
                                            Reddit
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey="buzz">
                                            Buzz
                                        </Nav.Link>
                                    </Nav.Item>
                                </Nav>
                            </Col>
                            <Col lg={12}>
                                <Tab.Content>
                                    <Tab.Pane eventKey="home" className="py-4">
                                        <div className="dataFundamental">
                                            <p>Based on Github and Developer Activity</p>
                                            <div className="score-data">
                                                <Row>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Fundamental Analysis Score</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.fundamental)}/10</p>
                                                        </div>
                                                    </Col>
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="technical" className="py-4">
                                        <div className="dataTechnical">
                                            <p>Based On Tech Spec</p>
                                            <div className="score-data">
                                                <Row>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Technical</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.technical)}/10</p>
                                                        </div>
                                                    </Col>
                                                    {/* <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">Movie Averages</p>
                                    <p className="scores">/</p>
                                </div>
                                </Col>
                                <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">Analysis Score</p>
                                    <p className="scores">/</p>
                                </div>
                                </Col> */}
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="news" className="py-4">
                                        <div className="dataNews">
                                            <p>News sentiment and volume that drives the asset prices and volatility</p>
                                            <div className="score-data">
                                                <Row>
                                                    <Col xs={12} sm={6} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Average News Sentiment</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.news_sentiment)}/10</p>
                                                        </div>
                                                    </Col>
                                                    <Col xs={12} sm={6} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">News Volume</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.news_volume)}</p>
                                                        </div>
                                                    </Col>
                                                    {/* <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">News Trend</p>
                                    <p className="scores">/</p>
                                </div>
                                </Col>
                                <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">News Popularity</p>
                                    <p className="scores">/</p>
                                </div>
                                </Col> */}
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="twitter" className="py-4">
                                        <div className="dataTwitter">
                                            <p>Based on twitter data </p>
                                            <div className="score-data">
                                                <Row>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Twitter Sentiment</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.twitter_sentiment)}/10</p>
                                                        </div>
                                                    </Col>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Twitter Volume</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.twitter_volume)}</p>
                                                        </div>
                                                    </Col>
                                                    {/* <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">Twitter Trend</p>
                                    <p className="scores">0.00</p>
                                </div>
                                </Col>
                                <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">Twitter Popularity</p>
                                    <p className="scores">0.00</p>
                                </div>
                                </Col> */}
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="reddit" className="py-4">
                                        <div className="dataReddit">
                                            <p>Number of Reddit posts and messages and their senitments in the last 24 hours</p>
                                            <div className="score-data">
                                                <Row>

                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Reddit Sentiment</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.reddit_sentiment)}/10</p>
                                                        </div>
                                                    </Col>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Reddit Volume</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.reddit_volume)}</p>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="buzz" className="py-4">
                                        <div className="dataBuzz">
                                            <p>Average trend based on activity on Twitter, Reddit, news websites and search engines</p>
                                            <div className="score-data">
                                                <Row>
                                                    <Col xs={12} sm={12} xl={3}>
                                                        <div className="data">
                                                            <p className="score-name">Buzz</p>
                                                            <p className="scores">{NumberFormat(this.state.scoreAnalyserData.buzz)}/10</p>
                                                        </div>
                                                    </Col>
                                                    {/* <Col xs={3} sm={3} xl={3}>
                                <div className="data">
                                    <p className="score-name">Search Engine Popularity</p>
                                    <p className="scores">/</p>
                                </div>
                                </Col> */}
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                    <Col xs={3} sm={3} xl={3}>

                                                    </Col>
                                                </Row>
                                            </div>
                                        </div>
                                    </Tab.Pane>
                                </Tab.Content>
                            </Col>
                        </Row>
                    </Tab.Container>
                </div>
            </div>
            <Footer/>
            </div>
            </>
        );
    }
}

export default ScoreAnalyserDetail;
