import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";
import coinsData from "./../../common/coins-data-admin-call";
import {
  faLongArrowAltDown,
  faLongArrowAltUp,
  faTrashAlt,
} from "@fortawesome/free-solid-svg-icons";
import DOMPurify from "dompurify";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Loader from "../shared/loader";
import { DropdownButton, Dropdown } from "@themesberg/react-bootstrap";
import axios from "axios";

const myHTML = `<p class="title-main"></p>
<ul class="li-link">
<li>Exclusive inside access to investment advice from our team of professional traders. </li>
<li>Our most experienced trader has record of 700x yearly ROI. Get legendary trading calls and insight from the man himself, right here.
</li>
</ul>`;

const mySafeHTML = DOMPurify.sanitize(myHTML, {
  ALLOWED_TAGS: ["h1", "p", "span", "ul", "li"],
  ALLOWED_ATTR: ["style", "class"],
});

interface AdminCallsState {
  cointType: string;
  coinsData: any[];
  idLoading: boolean;
  allCoinsData: any;
}

interface AdminCallsProps {
  webSocket: any;
  checkLogin: () => void;
  checkPayment: () => void;
}

// component for admin calls
class AdminCalls extends React.Component<AdminCallsProps, AdminCallsState> {
  constructor(props: AdminCallsProps) {
    super(props);
    props.checkLogin();
    props.checkPayment();
    this.state = {
      cointType: "",
      coinsData: [],
      idLoading: true,
      allCoinsData: {},
    };
  }

  componentDidMount() {
    document.title = "Admin Calls | Verox";
    this.props.webSocket.emit("getAdminCalls");
    this.props.webSocket.on("adminCalls", (response: any) => {
      console.log("rrrtttiii", response);
      let allIds: any = "";
      response.data.map((dataId: any, ind: any) => {
        if (ind === response.data.length - 1) {
          allIds = allIds + dataId.coin;
        } else {
          allIds = allIds + dataId.coin + ",";
        }
      });
      // console.log("allIds", allIds);
      axios
        .get(
          `https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${allIds}&order=market_cap_desc&per_page=100&page=1&sparkline=true`
        )
        .then((res) => {
          console.log("all-ccc", res.data);
          let allcoins = {};
          res.data.map((coinData: any) => {
            console.log("isTrue", coinData);
            allcoins = {
              ...allcoins,
              [coinData.id]: { ...coinData },
            };
          });
          console.log("all-ccc2", allcoins);
          this.setState({
            ...this.state,
            allCoinsData: allcoins,
          });
        });
      this.setState({ coinsData: response.data, idLoading: false });
    });

    //this.setState({ coinsData:coinsData,idLoading: false })
  }

  handleSelect(e: any) {
    this.setState({ cointType: e });
  }

  render() {
    //window.location.hash = "/markets";
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4>Admin Calls</h4>
                </div>
              </Container>
            </Navbar>

            <Row className="terms-of-use">
              <Col xs={12} sm={12} xl={12}>
                <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
              </Col>
            </Row>

            <div
              className="main-markets-table-head"
              style={{ textAlign: "left" }}
            >
              <h5>
                Filter by Direction
                <p
                  className="yield-rewards"
                  onClick={() => this.handleSelect("")}
                  style={{
                    cursor: "pointer",
                    background: this.state.cointType == "" ? "#823c6a" : "none",
                  }}
                >
                  All
                </p>
                <p
                  className="yield-rewards"
                  onClick={() => this.handleSelect("buy")}
                  style={{
                    cursor: "pointer",
                    background:
                      this.state.cointType == "buy" ? "#823c6a" : "none",
                  }}
                >
                  Buy
                </p>
                <p
                  className="yield-rewards"
                  onClick={() => this.handleSelect("sell")}
                  style={{
                    cursor: "pointer",
                    background:
                      this.state.cointType == "sell" ? "#823c6a" : "none",
                  }}
                >
                  Sell
                </p>
              </h5>
            </div>
            {!this.state.idLoading ? (
              <>
                {this.state.coinsData &&
                  this.state.coinsData
                    .filter(
                      (e: any) =>
                        this.state.cointType == "" ||
                        e.type.toLowerCase() ===
                          this.state.cointType.toLowerCase()
                    )
                    .map((coin: any, index: number) => {
                      var date = new Date(coin.ts);
                      date.toDateString();
                      console.log("lkop", date.toDateString());
                      console.log("check",this.state.allCoinsData &&
                      this.state.allCoinsData[coin.coin])
                      return (
                        <Row key={index} className="admin-call-data">
                          <Col
                            xs={12}
                            sm={12}
                            xl={1}
                            className="no-left-padding"
                          >
                            <div
                              className={`admin-call-tag ${
                                coin &&
                                coin.type &&
                                coin.type.toLowerCase() === "sell"
                                  ? "admin-call-tag-sell"
                                  : null
                              }`}
                            >
                              {coin.type}
                              {/* <div
                                className="coin-image"
                                style={{
                                  display: "inline",
                                  paddingLeft: "5px",
                                }}
                              >
                                <img
                                  src={
                                    this.state.allCoinsData &&
                                    this.state.allCoinsData[coin.coin] &&
                                    this.state.allCoinsData[coin.coin].image
                                  }
                                  alt={""}
                                />
                              </div> */}
                            </div>
                          </Col>
                          <Col
                            xs={12}
                            sm={12}
                            xl={5}
                            className="admin-call-coin-data"
                          >
                            <div className="coin-date">
                              {date.toDateString()}
                            </div>
                            <Row className="mt-4">
                              <Col xs={12} sm={12} xl={2}>
                                <div
                                  className="coin-image"
                                  style={{
                                    // display: "inline",
                                    paddingLeft: "5px",
                                    margin: "25px auto 0 auto",
                                  }}
                                >
                                  <img
                                    style={{
                                      width: "70%",
                                      height: "auto",
                                    }}
                                    src={
                                      this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin].image
                                    }
                                    alt={""}
                                  />
                                </div>
                              </Col>
                              <Col xs={12} sm={12} xl={6}>
                                <div
                                onClick={() =>
                                  (window.location.hash =
                                    "/coin/" + this.state.allCoinsData[coin.coin].symbol + "/" + this.state.allCoinsData[coin.coin].id)
                                }
                                >{/* fdf*/}
                                  <span className="admin-call-coin-name">
                                    {this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin].name}
                                  </span>
                                </div>
                                <div className="admin-call-coin-symbol">
                                  {this.state.allCoinsData &&
                                    this.state.allCoinsData[coin.coin] &&
                                    this.state.allCoinsData[coin.coin].symbol}
                                </div>
                              </Col>
                              <Col
                                xs={12}
                                sm={12}
                                xl={4}
                                className="coin-variant-col"
                              >
                                <span className="coin-variant">
                                  $
                                  {this.state.allCoinsData &&
                                    this.state.allCoinsData[coin.coin] &&
                                    this.state.allCoinsData[coin.coin]
                                      .current_price}
                                </span>
                                <div className="coin-variable">
                                  {this.state.allCoinsData &&
                                  this.state.allCoinsData[coin.coin] &&
                                  this.state.allCoinsData[coin.coin]
                                    .price_change_percentage_24h > 0 ? (
                                    <FontAwesomeIcon
                                      className="text-success"
                                      icon={faLongArrowAltUp}
                                    />
                                  ) : (
                                    <FontAwesomeIcon
                                      className="text-danger"
                                      icon={faLongArrowAltDown}
                                    />
                                  )}
                                  <span
                                    className={`ml-2 ${
                                      this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin]
                                        .price_change_percentage_24h > 0
                                        ? "text-success"
                                        : "text-danger"
                                    }`}
                                  >
                                    {this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin]
                                        .price_change_percentage_24h}
                                  </span>
                                </div>
                              </Col>
                            </Row>

                            {/* <Row>
                              <Col xs={12} sm={12} xl={8}>
                                <div className="admin-call-coin-symbol">
                                  {this.state.allCoinsData &&
                                    this.state.allCoinsData[coin.coin] &&
                                    this.state.allCoinsData[coin.coin].symbol}
                                </div>
                              </Col>
                              <Col xs={12} sm={12} xl={4}>
                                <div className="coin-variable">
                                  {this.state.allCoinsData &&
                                  this.state.allCoinsData[coin.coin] &&
                                  this.state.allCoinsData[coin.coin]
                                    .price_change_percentage_24h > 0 ? (
                                    <FontAwesomeIcon
                                      className="text-success"
                                      icon={faLongArrowAltUp}
                                    />
                                  ) : (
                                    <FontAwesomeIcon
                                      className="text-danger"
                                      icon={faLongArrowAltDown}
                                    />
                                  )}
                                  <span
                                    className={`ml-2 ${
                                      this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin]
                                        .price_change_percentage_24h > 0
                                        ? "text-success"
                                        : "text-danger"
                                    }`}
                                  >
                                    {this.state.allCoinsData &&
                                      this.state.allCoinsData[coin.coin] &&
                                      this.state.allCoinsData[coin.coin]
                                        .price_change_percentage_24h}
                                  </span>
                                </div>
                              </Col>
                            </Row> */}
                          </Col>
                          {/* <Col xs={12} sm={12} xl={1}></Col> */}
                          <Col xs={12} sm={12} xl={6}>
                            <Row>
                              <Col xs={12} sm={12} xl={4}>
                                <div
                                  className="admin-call-coin-price"
                                  style={{ width: "100%" }}
                                >
                                  <span className="admin-call-coin-price-label">
                                    Buy Price
                                  </span>
                                  <span className="admin-call-coin-price-value">
                                    ${coin.price}
                                  </span>
                                </div>
                              </Col>
                              <Col xs={12} sm={12} xl={4}>
                                <div
                                  className="admin-call-coin-price"
                                  style={{ width: "100%" }}
                                >
                                  <span className="admin-call-coin-price-label">
                                    Stop Loss
                                  </span>
                                  <span className="admin-call-coin-price-value">
                                    {coin.stoploss !== "-" ? "$" : ""}{" "}
                                    {coin.stoploss}
                                  </span>
                                </div>
                              </Col>
                              <Col xs={12} sm={12} xl={4}>
                                <div
                                  className="admin-call-coin-price"
                                  style={{ width: "100%" }}
                                >
                                  <span className="admin-call-coin-price-label">
                                    Target
                                  </span>
                                  <span className="admin-call-coin-price-value">
                                    ${coin.target}
                                  </span>
                                </div>
                              </Col>
                            </Row>
                          </Col>
                        </Row>
                      );
                    })}
              </>
            ) : (
              <Loader />
            )}
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default AdminCalls;
