import { Card, Col, Image, Modal, Nav, Row } from "@themesberg/react-bootstrap";
import {
  faMedium,
  faTelegram,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import React from "react";
import { Routes } from "../../common/routes";
import VeroxLogo from "../../assets/images/logo.png";
import apple from "../../assets/images/apple.png";
import gp from "../../assets/images/google-play.png";
import phone from "../../assets/images/phone.png";

interface FooterState {
  isContactUsVisible: boolean;
}

interface FooterProps {}

class Footer extends React.Component<FooterProps, FooterState> {
  constructor(props: FooterProps) {
    super(props);
    this.state = {
      isContactUsVisible: false,
    };
  }

  showContactUs() {
    this.setState({ isContactUsVisible: true });
  }

  hideContactUs() {
    this.setState({ isContactUsVisible: false });
  }

  faqLink() {
    if (window.location.href.indexOf(Routes.Markets.path) !== -1)
      return <NavItem link={Routes.FAQ.path} title="FAQ" />;
    return "";
  }

  render() {
    return (
      <div className="footers">
        <>
          <div className="footer-mobile-app">
            <Row>
              <Col xs={12} lg={6}>
                <div className="side-app-text">
                  <h4>
                    Experience the power of VeroxAI in the palm of your hands
                  </h4>
                  <p className="download-text">
                    Take the power of VeroxAI with you. Download the VeroxAI
                    crypto analytics app today.
                  </p>
                  <div className="playstr-img">
                    <a
                      target="_blank"
                      href="https://apps.apple.com/in/app/veroxai/id1572202642"
                    >
                      <img src={apple} alt="apple" />
                    </a>
                    <a
                      target="_blank"
                      href="https://play.google.com/store/apps/details?id=com.veroxai"
                    >
                      <img src={gp} alt="android" />
                    </a>
                  </div>
                </div>
              </Col>

              <Col md="auto" xs lg="6">
                <div className="img-app">
                  <img src={phone} alt="app" className="right" />
                </div>
              </Col>
            </Row>
          </div>
        </>
        <footer className="footer section py-5">
          <Row>
            <Col xs={12} lg={3} className="mb-4 mb-lg-0">
              <div className="FooterWidgetTitle">About</div>
              <div className="footer-menu">
                <Nav className="flex-column pt-3 pt-md-0 menu">
                  <NavItem title="About Us" link={Routes.AboutUs.path} />
                  <NavItem
                    title="Cookie Policy"
                    link={Routes.CookiePolicy.path}
                  />
                  <NavItem title="Terms Of Use" link={Routes.TermsOfUse.path} />
                  <NavItem
                    title="Privacy Policy"
                    link={Routes.PrivacyPolicy.path}
                  />
                  <NavItem title="Disclaimer" link={Routes.Disclaimer.path} />
                </Nav>
              </div>
            </Col>
            <Col xs={12} lg={3}>
              <div className="FooterWidgetTitle">Tools</div>
              <div className="footer-menu">
                <Nav className="flex-column pt-3 pt-md-0 menu">
                  <NavItem title="Admin Calls" link={Routes.AdminCalls.path} />
                  <NavItem
                    title="Score Analyzer"
                    link={Routes.ScoreAnalyser.path}
                  />
                  <NavItem
                    title="Biggest Volume Changes"
                    link={Routes.BiggestVolumeChanges.path}
                  />
                  <NavItem title="Top Picks" link={Routes.TopPicks.path} />
                  <NavItem
                    title="Yield Farming"
                    link={Routes.YieldFarming.path}
                  />
                </Nav>
              </div>
            </Col>
            <Col xs={12} lg={3}>
              <div className="FooterWidgetTitle">Support</div>
              <div className="footer-menu">
                <Nav className="flex-column pt-3 pt-md-0 menu">
                  <NavItem
                    title="Contact Us"
                    onClick={() => this.showContactUs()}
                  />
                  {this.faqLink()}
                </Nav>
              </div>
            </Col>
            <Col xs={12} lg={3}>
              <div className="FooterWidgetTitle">Follow</div>
              <div className="footer-menu">
                <ul className="social-media">
                  <li>
                    <a href="https://twitter.com/verox_aicrypto?s=11">
                      <FontAwesomeIcon icon={faTwitter} />
                    </a>
                  </li>
                  <li>
                    <a href="https://veroxai.medium.com/">
                      <FontAwesomeIcon icon={faMedium} />
                    </a>
                  </li>
                  <li>
                    <a href="https://t.me/veroxgroup">
                      <FontAwesomeIcon icon={faTelegram} />
                    </a>
                  </li>
                </ul>
              </div>
            </Col>
          </Row>
          <Row className="footer-2">
            <p className="mb-0 text-center text-xl-left">
              {/*  {`${currentYear} `} */}
              <Card.Link
                href="https://veroxai.com/"
                target="_blank"
                className="text-decoration-none fw-normal"
              >
                Verox AI
              </Card.Link>
              &nbsp;&nbsp;&copy;&nbsp; 2021
            </p>
          </Row>
          {/* <Signup
                modalOpen={modalOpen}
                handleModalOpen={handleModalOpen} /> */}
        </footer>
        <Modal
          className="auth-modal"
          size="sm"
          centered
          show={this.state.isContactUsVisible}
          backdrop="static"
          onHide={() => this.hideContactUs()}
        >
          <Modal.Header closeButton></Modal.Header>
          <Modal.Body>
            <div className="cropped">
              <Image src={VeroxLogo} height={100} />
            </div>
            <h3 className="contact-text">
              Please send your queries to team@veroxai.com
            </h3>
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}

function NavItem(props: any) {
  const { title, link, target, badgeText, onClick } = props;
  const classNames = badgeText
    ? "d-flex justify-content-start align-items-center justify-content-between"
    : "";

  return (
    <Nav.Item onClick={onClick}>
      {/* onClick={() => setShow(false)}> */}
      <Nav.Link as={Link} to={link} target={target} className={classNames}>
        <span>
          <span className="sidebar-text">{title}</span>
        </span>
      </Nav.Link>
    </Nav.Item>
  );
}

export default Footer;
