import React from "react";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { Form } from "@themesberg/react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Modal, ModalBody, ModalFooter,ModalHeader, Button } from "reactstrap";
import {  faClock, faCheckCircle ,faTimes } from "@fortawesome/free-solid-svg-icons";

interface MakeFavouritesState {
  modal: boolean;
  msg: String;
  isChecked:boolean;
  modalcheck:boolean,
  setfav :boolean,
  favlist:any[]
}

interface MakeFavouritesProps {
  webSocket: any;
  coin: any;
  callFromFavourite?: boolean;
  getFavourite?: any;
  showText :boolean;
  
  
}

class MakeFavourites extends React.Component<
  MakeFavouritesProps,
  MakeFavouritesState
> {
  havefav(){
    let have = false;
    this.state.favlist.map((fav:string)=>{
      if(fav.toLowerCase()===this.props.coin.id.toLowerCase()){
        have = true;
      }
    })
    return(have);
  }
  constructor(props: MakeFavouritesProps) {
    super(props);
    this.state = {
      modal: false,
      msg: "",
      modalcheck:false,
      isChecked:false,
      setfav:false,
      favlist:[]
    };
    this.props.webSocket.emit("getFavourite", {
      userId: localStorage.getItem("verox_uid")
    });
    this.props.webSocket.on("favourites", (response: any) => {
      
      if(response.data){
        // update data on parent page
        
        if (this.props.callFromFavourite) {
          this.props.getFavourite(response.data);
        }
        
          this.setState({favlist:response.data.coins},()=>{

            if(response.data.coins.length! > 0 && this.havefav()){
              
              this.setState({setfav:true})
            }else{
             
              this.setState({setfav:false})
            }
          })
          
        
      }else{
        this.setState({setfav:false})
      }
      
      
    });
  }

  toggle = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
    }));
    //window.location.reload();
  };

  togglecheck = () => {
    this.setState((prevState) => ({
      modalcheck: !prevState.modalcheck,
    }));
    //window.location.reload();
  };

  domodalcheck = (event:any)=>{
    if(this.havefav()){
        this.toggltFavourite(event);      
      }else{
       this.setState({ modalcheck: true });
      }
  }

  toggltFavourite(event: any) {
    this.setState({ modalcheck: false });
    event.preventDefault();
    event.stopPropagation();

    event.target.classList.add("favourite");
    let favlist=[];
    if (
      this.havefav()
    ) {
      favlist = this.state.favlist.filter((coinid:string)=>coinid.toLowerCase()!=this.props.coin.id.toLowerCase())
      
      this.setState({ msg: "Coin removed from favourites" });
      this.setState({ modal: true });
    } else {
      if(this.state.isChecked){
        const data = {
          userId: localStorage.getItem("verox_uid"),
          coin:this.props.coin.id,
          status:true
        }
        this.props.webSocket.emit("pushSignalAlert", data);
        this.setState({ msg: "Coin added to favourites.Alert modified." });
      }else{
        this.setState({ msg: "Coin added to favourites" });

      }
      favlist = this.state.favlist;
      favlist.push(this.props.coin.id);
      this.setState({ modal: true });
    }

    
    this.props.webSocket.emit("setFavourite", {
      userId: localStorage.getItem("verox_uid"),
      coins: favlist,
    });
  }
  render() {
    return (
      <>

          <div   className="heart">
          {this.state.setfav &&
           <FontAwesomeIcon
            style={{ fontSize: "25px",cursor: "pointer" }}
            icon={faHeart}
            onClick={(e) => { e.stopPropagation();this.domodalcheck(e)}}
            className="favourite"
          />
          }
          {!this.state.setfav &&
           <FontAwesomeIcon
            style={{ fontSize: "25px",cursor: "pointer" }}
            icon={faHeart}
            onClick={(e) => { e.stopPropagation();this.domodalcheck(e)}}
            className="favourite_none"
          />
          }
            
            {this.props.showText ? <span
              onClick={(e) => { e.stopPropagation();this.domodalcheck(e)}}
              style={{
                margin: "13px",
                cursor: "pointer",
                textTransform: "uppercase",
                fontWeight: 600,
                fontSize: "16px",
                color: "rgb(19,199,255)",
              }}
            >
              {this.state.setfav?"Remove from favourites":"Add to favourites"}
            </span> : <div />
          }
        </div>
        <Modal style={{top:"240px"}} isOpen={this.state.modalcheck} toggle={this.togglecheck}>
            <ModalHeader>
              <div style={{position:"absolute",width:"90%",textAlign:"right",marginRight:"1rem"}} onClick={()=>this.togglecheck()}>
              <FontAwesomeIcon  style={{fontSize:"40px",color:"white"}} icon={faTimes} />

              </div>
            <div className="main-coin-name">
                  <img src={this.props.coin.image} alt={this.props.coin.name} />
                  <h6>{this.props.coin.name} <br/> <span>{this.props.coin.symbol}</span></h6>
              </div>
              
            </ModalHeader>
            <ModalBody style={{fontSize:"40px"}}>
                <div>
                    
                    <Form.Group className="mt-4 d-inline-flex" controlId="formBasicCheckbox" style={{fontSize: 'x-large'}}>
                    <Form.Check onChange={(event) => this.setState({ isChecked: event.target.checked })} onClick={(e) => { e.stopPropagation();}} type="checkbox" label="Buy/Sell Signal Alert" />
                    </Form.Group>
                    <br/> 
                    <br/> 
                      <div className="login-card"> <Button onClick={(event) => this.toggltFavourite(event)} style={{width:"99%",border:"none",outline:"none",background:"transparent",boxShadow:"none",fontSize:"20px"}} >Set Favorite</Button></div>
                </div>
            </ModalBody>
        </Modal>
        <Modal
          style={{ top: "240px" }}
          isOpen={this.state.modal}
          toggle={this.toggle}
        >
          <ModalBody style={{ fontSize: "35px" }}>
            {this.state.msg}
            <br />
            <br />
            <Button variant="secondary" className="btn-lg  mt-4" onClick={this.toggle} style={{
                background: "linear-gradient(to right, #9D6BFF, #FF6CCD)",
                outline:"none",
                padding: "15px 50px",
                borderRadius:"30px",
                color: "#fff",
                textTransform:"uppercase",
                fontSize: "15px",
                width: "200px",
                borderColor:"#2e3650",
              }}>Okay</Button>
          </ModalBody>
        </Modal>
      </>
    );
  }
}

export default MakeFavourites;
