import { Col, Container, Navbar, Row } from '@themesberg/react-bootstrap';
import React from "react";
import DOMPurify from "dompurify";
import Sidebar from "../../shared/sidebar";
import Footer from "../../shared/footer";

class AboutUs extends React.Component<{webSocket: any}, {}> {

  componentDidMount() {
  }

  render(): any {
    document.title = `About Us | Verox`; 
    const myHTML = `<p class="title-main"></p>
                    <p class="desc-main">Verox is a crypto and DeFi protocol for all financial needs in this ground-breaking ecosystem.</p>
                    <p class="title-main">As for now, The Verox project includes VeroxAI app and Verox DEX:</p>
                    <p class="desc-main">VeroxAI is an app created to give both new and professional crypto traders more insights about crypto and DeFi. This will in turn, save their time, efforts and allow them to make better decisions in the market.It is a personal advisor, product analyzer and financial content provider. Using AI and deep learning, we create a personalized match between the user and their financial needs in both crypto trading and yield farming. VeroxAI analyzes any chosen token and finds the best investments in crypto and yield farming in real time, by scanning markets and processing massive sets of data to locate the optimal trading/investment opportunities.</p>
                    <p class="desc-main">Verox DEX is the worlds first AI-powered decentralized cryptocurrency and derivatives exchange with next generation AMM and orderbook. VeroxAI and Verox DEX will be connected through algorithmic trading bots, to allow automated trading for our AI algorithms on the DEX.</p> `;
    const mySafeHTML = DOMPurify.sanitize(myHTML, {
      ALLOWED_TAGS: ["h1", "p", "span", "ul", "li"],
      ALLOWED_ATTR: ["style","class"],
    });
    return (
      <>
      <Sidebar webSocket={this.props.webSocket}/>
      <div className="app-container-with-sidebar">
        <div className="component">
        <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4>About Us</h4>
            </div>
          </Container>
        </Navbar>

        <div className="updated-on">
          <div className="header-class justify-content-between w-100">
            <p>Updated on 2021-05-23</p>
          </div>
        </div>

        <Row className="terms-of-use">
          <Col xs={12} sm={12} xl={12}>
            <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
          </Col>
          
        </Row>
          </div>
          <Footer/>
        </div>x
      </>
    );
  }
}

export default AboutUs;