import { Container, Navbar, Row } from "@themesberg/react-bootstrap";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import PaymentModal from "./paymentmodal"
import axios from "axios";

interface PaymentPlanState {
  islogin:boolean;
  modal: boolean;
  buytype:number;
  amountStart:number;
  amountPro:number;

}

interface PaymentPlanProps {
    showSignUpAuthModal:()=>void,
    webSocket: any,
    checkLogin: ()=>void,
    recheckpayment:()=>void
}

// dashboard component
class PaymentPlan extends React.Component<PaymentPlanProps, PaymentPlanState> {

  constructor(props: PaymentPlanProps) {
    super(props);
    this.state = {
      islogin:false,
      modal:false,
      buytype:-1,
      amountStart:0,
      amountPro:0
    };
    
  }

  apiKey = 'BQYvhnv04csZHaprIBZNwtpRiDIwEIW9' // replace this with your API Key

  // get latest WBNB-BUSD price for conversion
  queryPrice = `
  query ($baseCurrency: String, $quoteCurrency: String) {
    ethereum(network: ethereum) {
      dexTrades(
        baseCurrency: {is: $baseCurrency}
        quoteCurrency: {is: $quoteCurrency}
        options: {desc: ["block.height", "transaction.index"], limit: 1}
      ) {
        block {
          height
          timestamp {
            time(format: "%Y-%m-%d %H:%M:%S")
          }
        }
        transaction {
          index
        }
        baseCurrency {
          symbol
        }
        quoteCurrency {
          symbol
        }
        quotePrice
      }
    }
  }
  `

  showSignup=()=>{
    this.props.showSignUpAuthModal();
  }

  setModal = (status:any)=>{
    this.setState({modal:status})
  }
  
  buyNow=(type:number)=>{
    this.setState({buytype:type});
    this.setState({modal:true})
  }
  
  getVeroxPrice = async() => {
    try{
      const bitqueryClient = axios.create({
        baseURL: 'https://graphql.bitquery.io',
        headers: { 'X-API-KEY': this.apiKey }
      })

      let res = await bitqueryClient.post(
      '/', 
      { query: this.queryPrice,
        variables: {
          baseCurrency: "0x87de305311d5788e8da38d19bb427645b09cb4e5",
          quoteCurrency:"0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2"
        } 
      });
      const veroxeth = res?.data?.data?.ethereum?.dexTrades?.[0]?.quotePrice;
      res = await bitqueryClient.post(
      '/', 
      { query: this.queryPrice,
        variables: {
          baseCurrency: "0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2",
          quoteCurrency:"0xdac17f958d2ee523a2206206994597c13d831ec7"
        } 
      });
      const wethusd = res?.data?.data?.ethereum?.dexTrades?.[0]?.quotePrice;
      const veroxusd = Number(veroxeth)*Number(wethusd);
      this.setState({amountStart:Number((120/veroxusd).toFixed(2))});
      this.setState({amountPro:Number((200/veroxusd).toFixed(2))});
    }catch(e){
      console.log(e);
    }
  }
  componentDidMount() {
    document.title = "Payment Plan | Verox";
    if (localStorage.getItem("verox_uid")) {
      console.log('localStorage.getItem("verox_uid")',localStorage.getItem("verox_uid"));
      this.setState({islogin:true});
    }
    this.getVeroxPrice();
  }

  
  render() {
    return (
        <>
        <Sidebar webSocket={this.props.webSocket}/>
          <div className="app-container-with-sidebar">
            <div className="component">
              <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4 style={{ fontSize: "2.25rem"}}>Payment Plan</h4>
              {/*<h4>Hi, {userInfo.username}!</h4>*/}
              <p>Please pay monthly fee by verox token.</p>
            </div>
          </Container>
        </Navbar>

        
         
        
            
        <div className="row" style={{"marginTop":"100px"}}>
                    <div className="col-lg-1 col-md-1 col-0 mb-lg-0 mb-md-2"></div>
                    <div className="col-lg-5 col-md-4 col-12 mb-lg-0 mb-md-3">
                        <div className="nimmu-price-card">
                            <h4>Starter Plan(checked checked)</h4>
                            <h2 >{this.state.amountStart} Verox <strong>/</strong><span>per month</span></h2>
                            <ul className="text-left text-white">
                                <li>Access to all TradingView Charts</li>
                                <li>Access to VeroxAI Real Time Signals</li>
                                <li>Access to Price Alerts</li>
                                <li>Access to Buy and Sell Alerts</li>
                                <li>Access to VeroxAIs Top Picks, Most Mentioned, Biggest Volume Changes</li>
                                <li>Access to Passive Income</li>
                                <li>Voting </li>
                                <li>Get live Admin Calls</li>
                                <li>&nbsp;</li>
                                <li>&nbsp;</li>
                                <li>&nbsp;</li>
                            </ul>
                            {this.state.islogin?
                              <button className="btn nimmu-btn nimmu-btn-defaul"  onClick={()=>{this.buyNow(1)}}>Buy Now</button>
                              :
                              <button className="btn nimmu-btn nimmu-btn-defaul" onClick={this.showSignup}>Signup</button>
                            }
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-4 col-12 mb-lg-0 mb-md-3">
                        <div className="nimmu-price-card">
                            <div className="tag">popular</div>
                            <h4>Pro Plan</h4>
                            <h2>{this.state.amountPro} Verox <strong>/</strong><span>per month</span></h2>
                            <ul className="text-left text-white">
                                <li>Access to all TradingView Charts</li>
                                <li>Access to VeroxAI Real Time Signals</li>
                                <li>Access to Price Alerts</li>
                                <li>Access to Buy and Sell Alerts</li>
                                <li>Access to VeroxAIs Top Picks, Most Mentioned, Biggest Volume Changes</li>
                                <li>Access to Passive Income</li>
                                <li>Voting </li>
                                <li>Get live Admin Calls</li>
                                <li>Access to Score Analyzer</li>
                                <li>Access to Best Long Term Crypto Investment</li>
                                <li>VeroxAIs Automated Trading Bot</li>
                            </ul>
                            {this.state.islogin?
                              <button className="btn nimmu-btn nimmu-btn-defaul"  onClick={()=>{this.buyNow(2)}}>Buy Now</button>
                              :
                              <button className="btn nimmu-btn nimmu-btn-defaul" onClick={this.showSignup}>Signup</button>
                            }
                        </div>
                    </div>
          </div>

        </div>
         <PaymentModal modalStatus={this.state.modal} 
            modalSet={this.setModal} 
            buyType={this.state.buytype} 
            payAmount = {this.state.buytype==1?this.state.amountStart:this.state.amountPro}
            recheckpayment={this.props.recheckpayment}/>
        <Footer/>
        </div>
      </>
    );
  }
}

export default PaymentPlan;
