/*TODO :  move css to appropriate place */
import 'react-phone-number-input/style.css'

import { Button, Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";

import Footer from "../shared/footer";
import PhoneInput from 'react-phone-number-input'
import React from "react";
import Sidebar from "../shared/sidebar";
import { faCheckCircle  } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {  Modal, ModalBody } from "reactstrap"

interface SettingsState {
  checked: boolean,
  userDetails: any,
  name: any,
  mobile: any,
  password: any,
  email: any,
  confirmPassword: any,
  error: string,
  modal:boolean
}

interface SettingsProps {
  webSocket: any,
  checkLogin: () => void
}

class Settings extends React.Component<SettingsProps, SettingsState> {
  constructor(props: SettingsProps) {
    super(props);
    props.checkLogin();
    this.state = {
     
      checked: true,
      userDetails: [],
      name: "",
      email: "",
      mobile: "",
      password: "",
      confirmPassword: "",
      error: "",
      modal:false
    }
  }

  componentDidMount() {
    this.props.webSocket.on("userDetails", (data: any) => {
      console.log("userDetails",data);
      if (data && data.data)
        this.setState({ userDetails: data.data, name: data.data.name, mobile: data.data.mobile, email: data.data.email });
      this.props.webSocket.removeAllListeners("userDetails");
    });
    this.props.webSocket.emit("getUserDetails", {
      userId: localStorage.getItem("verox_uid")
    });
  }

  handleOnChange(event: { target: { name: any; value: any; }; }) {
    this.setState({ ...this.state, [event.target.name]: event.target.value });
  }

  toggle = () => {
  
    this.setState(prevState => ({
      modal: !prevState.modal
    }));
  //   window.location.reload()
         
  }
  savePasswordDetails() {

    const { password, confirmPassword } = this.state;
    // perform all neccassary validations
    if (password !== confirmPassword) {
      this.setState({ error: "Passwords don't match" });


    } else {
      this.setState({ error: "" });

      console.log(this.state.mobile);
      console.log(this.state.password);
      this.props.webSocket.on("userDetails", (data: any) => {
        console.log("data");
        console.log(data);
        this.props.webSocket.removeAllListeners("userDetails");

      });
      this.props.webSocket.emit("pushUserDetials", {
        userId: localStorage.getItem("verox_uid"),
        password: this.state.password,
      });
    }

  }

  savePersonalDetails() {
    this.props.webSocket.on("userDetails", (data: any) => {
      console.log("data");
      console.log(data);
      this.props.webSocket.removeAllListeners("userDetails");

    });
    this.props.webSocket.emit("pushUserDetials", {
      userId: localStorage.getItem("verox_uid"),
      name: this.state.name,
      email: this.state.email,
      mobile: this.state.mobile
    });
    this.setState({modal:true})
  }

  render() {
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4>Settings</h4>
                  <p>Edit your profile</p>
                </div>
              </Container>
            </Navbar>

            <Row className="settings mt-4">
              <Col xs={5} sm={5} xl={5} className="">
                <div className="settings-title">
                  <h4>Profile</h4>
                </div>
                <div className="form-group">
                  <div className="form-label">Full Name</div>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter full name"
                    name="name"
                    value={this.state.name}
                    onChange={(event) => this.handleOnChange(event)}
                  />
                </div>

                <div className="form-group">
                  <div className="form-label">Email Address</div>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter email address"
                    name="email"
                    value={this.state.email}
                    onChange={(event) => this.handleOnChange(event)}
                  />
                </div>

                <div className="form-group">
                  <div className="form-label">Mobile Number</div>
                  <div className="form-control">
                    <PhoneInput
                      
                      placeholder="Enter phone number"
                      value={this.state.mobile}
                      onChange={mobile => this.setState({ mobile })} />
                  </div>
                </div>
              </Col>
              <Col xs={1} sm={1} xl={1} className="">
              </Col>

            </Row>

            <Row className="settings mt-4">
              <Col xs={5} sm={5} xl={5} className="text-center">
                <Button variant="secondary" className="btn-lg  mt-4" onClick={() => this.savePersonalDetails()}>Save</Button>
              </Col>
              <Col xs={1} sm={1} xl={1} className="">
              </Col>

            </Row>
          </div>

          <Modal style={{top:"240px"}} isOpen={this.state.modal} toggle={this.toggle}>
            <ModalBody style={{ fontSize: "35px" }}>
            <div style={{marginTop:"50px"}}>
              <FontAwesomeIcon style={{fontSize:"45px",color:'green'}} icon={faCheckCircle} />
                 <br></br>
                  Saved Settings.
              </div>
            <br />
            <br />
            <Button variant="secondary" className="btn-lg  mt-4" onClick={this.toggle} style={{
                background: "linear-gradient(to right, #9D6BFF, #FF6CCD)",
                outline:"none",
                padding: "15px 50px",
                borderRadius:"30px",
                color: "#fff",
                textTransform:"uppercase",
                fontSize: "15px",
                width: "200px",
                borderColor:"#2e3650",
              }}>Okay</Button>
           
          </ModalBody>


      
         
        </Modal>
          <Footer />
        </div>
      </>
    )
  }
}

export default Settings;