import { Card, Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";
import { Sparklines, SparklinesLine } from "react-sparklines";
import {
  faAngleDown,
  faAngleUp,
  faEye,
} from "@fortawesome/free-solid-svg-icons";
import { Modal, ModalBody, ModalFooter, Button } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Footer from "../shared/footer";
import Loader from "../shared/loader";
import { NumberFormat } from "../../common/number-format";
import React from "react";
import Sidebar from "../shared/sidebar";
import axios from "axios";
import MakeFavourites from "./make-favourite";

const dateFormat = require("dateformat");

interface DashboardState {
  topPicksData: any[];
  isTopPicksDataLoading: boolean;
  mostMentionedData: any[];
  isMostMentionedDataLoading: boolean;
  BVCData: any[];
  isBVCDataLoading: boolean;
  newsData: any[];
  isNewsLoading: boolean;
  marketData: any[];
  isMarketDataLoading: boolean;
  btcDominance: number;
  etheriumData: any[];
  bitcoinData: any[];
  veroxData: any[];
  modal: boolean;
  msg: String;
}

interface DashboardProps {
  webSocket: any;
  checkLogin: () => void;
  checkPayment: () => void;
}

// dashboard component
class Dashboard extends React.Component<DashboardProps, DashboardState> {
  constructor(props: DashboardProps) {
    super(props);
    this.state = {
      topPicksData: [],
      isTopPicksDataLoading: true,
      mostMentionedData: [],
      isMostMentionedDataLoading: true,
      BVCData: [],
      isBVCDataLoading: true,
      newsData: [],
      isNewsLoading: true,
      marketData: [],
      isMarketDataLoading: true,
      btcDominance: 0,
      etheriumData: [],
      bitcoinData: [],
      veroxData: [],
      modal: false,
      msg: "",
    };
  }

  toggle = () => {
    this.setState({ modal: !this.state.modal });
  };

  componentDidMount() {
    this.props.checkLogin();
    this.props.checkPayment();
    document.title = "Dashboard | Verox";
    axios
      .get(
        "https://api.veroxai.io/toppicks"
      )
      .then((response: any) => {
      if (response.data) {
        this.setState({
          topPicksData: response.data.coins ? response.data.coins.slice(0, 20) : [],
          isTopPicksDataLoading: false,
        });
        // this.props.webSocket.removeAllListeners("topPickTokens"); // this is just one-time listener
      }
    });

    axios
      .get(
        "https://api.veroxai.io/trending"
      )
      .then((response: any) => {
        if (response.data) {
          this.setState({
            mostMentionedData: response.data.coins ? response.data.coins.slice(0, 10) : [],
            isMostMentionedDataLoading: false,
          });
        }
      });

    // this.props.webSocket.emit("getMostMentioned", {});
    // this.props.webSocket.on("mostMentionedTokens", (response: any) => {
    //   if (response.data) {
    //     this.setState({
    //       mostMentionedData: response.data.slice(0, 10),
    //       isMostMentionedDataLoading: false,
    //     });
    //     this.props.webSocket.removeAllListeners("mostMentionedTokens"); // this is just one-time listener
    //   }
    // });

    axios
      .get(
        "https://api.veroxai.io/biggestvolme/change/"
      )
      .then((response: any) => {
        if (response.data) {
          this.setState({
            BVCData: response.data && response.data.coins ? response.data.coins.slice(0, 10) : [],
            isBVCDataLoading: false,
          });
        }
      });

    // this.props.webSocket.emit("getBiggestVolumeChange", {});
    // this.props.webSocket.on("biggestVolumeChangeTokens", (response: any) => {
    //   if (response.data) {
    //     this.setState({
    //       BVCData: response.data.slice(0, 10),
    //       isBVCDataLoading: false,
    //     });
    //     this.props.webSocket.removeAllListeners("biggestVolumeChangeTokens"); // this is just one-time listener
    //   }
    // });

    axios.get("https://api.veroxai.io/global/").then((response) => {
      console.log("split: ", response.data)
      this.setState({ 
        marketData: [response.data], isMarketDataLoading: false,
        btcDominance: response.data && response.data.dominances ? response.data.dominances.split(/%|:/)[1] : "",
      });
    });

    // axios.get("https://api.coinlore.net/api/global/").then((response) => {
    //   this.setState({ marketData: response.data, isMarketDataLoading: false });
    // });

    // axios.get("https://api.coingecko.com/api/v3/global").then((response) => {
    //   this.setState({
    //     btcDominance: response.data.data.market_cap_percentage.btc,
    //   });
    // });

    axios
      .get(
        "https://api.veroxai.io/watchlist?vs_currency=usd&ids=ethereum&order=market_cap_desc&per_page=100&page=1&sparkline=true"
      )
      .then((response) => {
        this.setState({ etheriumData: response.data.coins });
      });

    axios
      .get(
        "https://api.veroxai.io/watchlist?vs_currency=usd&ids=bitcoin&order=market_cap_desc&per_page=100&page=1&sparkline=true"
      )
      .then((response) => {
        this.setState({ bitcoinData: response.data.coins });
      });

    axios
      .get(
        "https://api.veroxai.io/watchlist?vs_currency=usd&ids=verox&order=market_cap_desc&per_page=100&page=1&sparkline=true"
      )
      .then((response) => {
        this.setState({ veroxData: response.data.coins });
      });

    axios
      .get(
        "https://api.veroxai.io/news?section=alltickers&items=50&token=oeptfszwnpuygimipibfxz4pxita7q0jmosubogt"
      )
      .then((response: any) => {
        this.setState({
          newsData: response.data.slice(0, 4),
          isNewsLoading: false,
        });
      });
  }

  render() {
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4 style={{ fontSize: "2.25rem" }}>Dashboard</h4>
                  {/*<h4>Hi, {userInfo.username}!</h4>*/}
                  <p>Track the entire crypto market in real-time</p>
                </div>
              </Container>
            </Navbar>

            <div className="dashboard-data">
              <div className="table-header">
                <Row className="justify-content-md-center ">
                  <Col xs={6} sm={6} xl={6}>
                    <h5>Markets</h5>
                  </Col>
                  <Col xs={6} sm={6} xl={6}></Col>
                </Row>
              </div>
              <Row className="justify-content-md-center main-markets-data">
                <Col xs={12} sm={6} xl={4} className="mb-4">
                  <Card border="light" className="shadow-sm">
                    <Card.Body style={{ cursor: "default" }}>
                      <Row className="d-block d-xl-flex align-items-center">
                        <Col xs={12} xl={12} className="px-xl-0">
                          <div className="d-sm-block">
                            <h5>
                              Market Cap <span className="small-cat"></span>
                            </h5>
                            {this.state.marketData[0] && (
                              <>
                                {this.state.marketData[0].marketcap > 0 ? (
                                  <h3 className="mb-1">
                                    $
                                    {NumberFormat(
                                      this.state.marketData[0].marketcap
                                    )}
                                    <FontAwesomeIcon
                                      icon={faAngleUp}
                                      className={`text-success me-1`}
                                    />
                                    <span className={`text-success fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0].mcap_change
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                ) : (
                                  <h3 className="mb-1">
                                    $
                                    {NumberFormat(
                                      this.state.marketData[0].marketcap
                                    )}
                                    <FontAwesomeIcon
                                      icon={faAngleDown}
                                      className={`text-danger me-1`}
                                    />
                                    <span className={`text-danger fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0].mcap_change
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                )}
                              </>
                            )}
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>
                  </Card>
                </Col>

                <Col xs={12} sm={6} xl={4} className="mb-4">
                  <Card border="light" className="shadow-sm">
                    <Card.Body style={{ cursor: "default" }}>
                      <Row className="d-block d-xl-flex align-items-center">
                        <Col xs={12} xl={12} className="px-xl-0">
                          <div className="d-sm-block">
                            <h5>24H VOLUME</h5>
                            {this.state.marketData[0] && (
                              <>
                                {this.state.marketData[0].volume > 0 ? (
                                  <h3 className="mb-1">
                                    $
                                    {NumberFormat(
                                      this.state.marketData[0].volume
                                    )}
                                    <FontAwesomeIcon
                                      icon={faAngleUp}
                                      className={`text-success me-1`}
                                    />
                                    <span className={`text-success fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0].volume_change
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                ) : (
                                  <h3 className="mb-1">
                                    $
                                    {NumberFormat(
                                      this.state.marketData[0].volume
                                    )}
                                    <FontAwesomeIcon
                                      icon={faAngleDown}
                                      className={`text-danger me-1`}
                                    />
                                    <span className={`text-danger fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0].volume_change
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                )}
                              </>
                            )}
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>
                  </Card>
                </Col>

                <Col xs={12} sm={6} xl={4} className="mb-4">
                  <Card border="light" className="shadow-sm">
                    <Card.Body style={{ cursor: "default" }}>
                      <Row className="d-block d-xl-flex align-items-center">
                        <Col xs={12} xl={12} className="px-xl-0">
                          <div className="d-sm-block">
                            <h5>BTC DOMINANCE</h5>
                            {this.state.marketData[0] && (
                              <>
                                {this.state.btcDominance >
                                0 ? (
                                  <h3 className="mb-1">
                                    {NumberFormat(this.state.btcDominance)}%
                                    <FontAwesomeIcon
                                      icon={faAngleUp}
                                      className={`text-success me-1`}
                                    />
                                    <span className={`text-success fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0]
                                          .avg_change_percent
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                ) : (
                                  <h3 className="mb-1">
                                    {NumberFormat(this.state.btcDominance)}%
                                    <FontAwesomeIcon
                                      icon={faAngleDown}
                                      className={`text-danger me-1`}
                                    />
                                    <span className={`text-danger fw-bold`}>
                                      {/* {NumberFormat(
                                        this.state.marketData[0]
                                          .avg_change_percent
                                      )} */}
                                      0%
                                    </span>
                                  </h3>
                                )}
                              </>
                            )}
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
              <Row className="justify-content-md-center main-markets-data second-market-dashboard">
                <Col
                  xs={12}
                  sm={6}
                  xl={4}
                  className="mb-4 handCursor"
                  onClick={() => (window.location.hash = "/coin/eth/ethereum")}
                >
                  <Row className="d-block d-xl-flex align-items-center">
                    <Col xs={12} xl={12} className="px-xl-0">
                      {this.state.etheriumData &&
                        this.state.etheriumData.map((data) => {
                          return (
                            <div className="d-sm-block" key={data.symbol}>
                              <h5>
                                {data.name} ({data.symbol})
                              </h5>
                              {data.price_change_percentage_24h > 0 ? (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleUp}
                                    className={`text-success me-1`}
                                  />
                                  <span className={`text-success fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              ) : (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleDown}
                                    className={`text-danger me-1`}
                                  />
                                  <span className={`text-danger fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              )}
                              <Sparklines data={data.sparkline_in_7d.price}>
                                <SparklinesLine color={"rgb(18,199,255)"} />
                              </Sparklines>
                            </div>
                          );
                        })}
                    </Col>
                  </Row>
                </Col>

                <Col
                  xs={12}
                  sm={6}
                  xl={4}
                  className="mb-4 handCursor"
                  onClick={() => (window.location.hash = "/coin/btc/bitcoin")}
                >
                  <Row className="d-block d-xl-flex align-items-center">
                    <Col xs={12} xl={12} className="px-xl-0">
                      {this.state.bitcoinData &&
                        this.state.bitcoinData.map((data) => {
                          return (
                            <div className="d-sm-block" key={data.symbol}>
                              <h5>
                                {data.name} ({data.symbol})
                              </h5>
                              {data.price_change_percentage_24h > 0 ? (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleUp}
                                    className={`text-success me-1`}
                                  />
                                  <span className={`text-success fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              ) : (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleDown}
                                    className={`text-danger me-1`}
                                  />
                                  <span className={`text-danger fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              )}
                              <Sparklines data={data.sparkline_in_7d.price}>
                                <SparklinesLine color={"rgb(18,199,255)"} />
                              </Sparklines>
                            </div>
                          );
                        })}
                    </Col>
                  </Row>
                </Col>

                <Col
                  xs={12}
                  sm={6}
                  xl={4}
                  className="mb-4 handCursor"
                  onClick={() => (window.location.hash = "/coin/vrx/verox")}
                >
                  <Row className="d-block d-xl-flex align-items-center">
                    <Col xs={12} xl={12} className="px-xl-0">
                      {this.state.veroxData &&
                        this.state.veroxData.map((data) => {
                          return (
                            <div className="d-sm-block" key={data.symbol}>
                              <h5>
                                {data.name} ({data.symbol})
                              </h5>
                              {data.price_change_percentage_24h > 0 ? (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleUp}
                                    className={`text-success me-1`}
                                  />
                                  <span className={`text-success fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              ) : (
                                <h3 className="mb-1">
                                  ${NumberFormat(data.current_price)}
                                  <FontAwesomeIcon
                                    icon={faAngleDown}
                                    className={`text-danger me-1`}
                                  />
                                  <span className={`text-danger fw-bold`}>
                                    {NumberFormat(
                                      data.price_change_percentage_24h
                                    )}
                                    %
                                  </span>
                                </h3>
                              )}
                              <Sparklines data={data.sparkline_in_7d.price}>
                                <SparklinesLine color={"rgb(18,199,255)"} />
                              </Sparklines>
                            </div>
                          );
                        })}
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>

            <div className="dashboard-data">
              <div className="table-header">
                <Row className="justify-content-md-center ">
                  <Col xs={6} sm={6} xl={6}>
                    <h5>Top Picks</h5>
                  </Col>
                  <Col xs={6} sm={6} xl={6}>
                    <div
                      className="view-all-dashboard"
                      onClick={() => (window.location.hash = "/toppicks")}
                    >
                      <FontAwesomeIcon icon={faEye} /> View All
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="table-header-2">
                <Row className="justify-content-md-center main-markets-table-head">
                  <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                    <h5 className="main-head-coin-d">Coin</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>Price</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>24H Change</h5>
                  </Col>
                  <Col xs={1} sm={1} xl={1} className="mb-3">
                    <h5>Add to favourites</h5>
                  </Col>
                  <Col xs={2} sm={2} xl={2} className="mb-3">
                    <h5>Market Cap</h5>
                  </Col>
                </Row>
              </div>

              <div className="main-table-cap">
                {this.state.isTopPicksDataLoading && <Loader />}
                {!this.state.isTopPicksDataLoading &&
                  this.state.topPicksData &&
                  this.state.topPicksData.map((data, index) => {
                    return (
                      <Row
                        key={data.symbol}
                        onClick={() =>
                          (window.location.hash =
                            "#/coin/" + data.symbol + "/" + data.id)
                        }
                        className="justify-content-md-center"
                      >
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <table className="main-coin-name">
                            <tr>
                              <td>
                                <img src={data.image} alt={data.name} />
                              </td>
                              <td>
                                <h6>
                                  {data.name} <br /> <span>{data.symbol}</span>
                                </h6>{" "}
                              </td>
                            </tr>
                          </table>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.current_price)}
                          </p>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <div className="chart-coin-name">
                            {data.market_cap_change_percentage_24h > 0 ? (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleUp}
                                  className={`text-success me-1`}
                                />
                                <span className={`text-success fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            ) : (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleDown}
                                  className={`text-danger me-1`}
                                />
                                <span className={`text-danger fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            )}
                            <Sparklines data={data.sparkline_in_7d.price}>
                              <SparklinesLine color={"rgb(18,199,255)"} />
                            </Sparklines>
                          </div>
                        </Col>
                        <Col
                          xs={1}
                          sm={1}
                          xl={1}
                          className="mb-3"
                          style={{ zIndex: 9999 }}
                        >
                          <MakeFavourites
                            webSocket={this.props.webSocket}
                            coin={data}
                            showText={false}
                          />
                        </Col>
                        <Col xs={2} sm={2} xl={2} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.market_cap)}
                          </p>
                        </Col>
                      </Row>
                    );
                  })}

                {!this.state.isTopPicksDataLoading &&
                  this.state.topPicksData.length === 0 && (
                    <Row className="justify-content-md-center">
                      <Col xs={12} sm={12} xl={12}>
                        <div className="no-data">
                          <h6>No data found for toppicks.</h6>
                        </div>
                      </Col>
                    </Row>
                  )}
              </div>
            </div>

            <div className="dashboard-data">
              <div className="table-header">
                <Row className="justify-content-md-center ">
                  <Col xs={6} sm={6} xl={6}>
                    <h5>Most Mentioned</h5>
                  </Col>
                  <Col xs={6} sm={6} xl={6}>
                    <div
                      className="view-all-dashboard"
                      onClick={() => (window.location.hash = "/most-mentioned")}
                    >
                      <FontAwesomeIcon icon={faEye} /> View All
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="table-header-2">
                <Row className="justify-content-md-center main-markets-table-head">
                  <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                    <h5 className="main-head-coin-d">Coin</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>Price</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>24H Change</h5>
                  </Col>
                  <Col xs={1} sm={1} xl={1} className="mb-3">
                    <h5>Add to favourites</h5>
                  </Col>
                  <Col xs={2} sm={2} xl={2} className="mb-3">
                    <h5>Market Cap</h5>
                  </Col>
                </Row>
              </div>

              <div className="main-table-cap">
                {this.state.isMostMentionedDataLoading && <Loader />}
                {!this.state.isMostMentionedDataLoading &&
                  this.state.mostMentionedData &&
                  this.state.mostMentionedData.map((data, index) => {
                    return (
                      <Row
                        key={data.symbol}
                        onClick={() =>
                          (window.location.hash =
                            "/coin/" + data.symbol + "/" + data.id)
                        }
                        className="justify-content-md-center"
                      >
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <table className="main-coin-name">
                            <tr>
                              <td>
                                <img src={data.image} alt={data.name} />
                              </td>
                              <td>
                                <h6>
                                  {data.name} <br /> <span>{data.symbol}</span>
                                </h6>{" "}
                              </td>
                            </tr>
                          </table>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.current_price)}
                          </p>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <div className="chart-coin-name">
                            {data.market_cap_change_percentage_24h > 0 ? (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleUp}
                                  className={`text-success me-1`}
                                />
                                <span className={`text-success fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            ) : (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleDown}
                                  className={`text-danger me-1`}
                                />
                                <span className={`text-danger fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            )}
                            <Sparklines data={data.sparkline_in_7d.price}>
                              <SparklinesLine color={"rgb(18,199,255)"} />
                            </Sparklines>
                          </div>
                        </Col>
                        <Col xs={1} sm={1} xl={1} className="mb-3">
                          <MakeFavourites
                            showText={false}
                            webSocket={this.props.webSocket}
                            coin={data}
                          />
                        </Col>
                        <Col xs={2} sm={2} xl={2} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.market_cap)}
                          </p>
                        </Col>
                      </Row>
                    );
                  })}

                {!this.state.isMostMentionedDataLoading &&
                  this.state.mostMentionedData.length === 0 && (
                    <Row className="justify-content-md-center">
                      <Col xs={12} sm={12} xl={12}>
                        <div className="no-data">
                          <h6>No data found most mentioned.</h6>
                        </div>
                      </Col>
                    </Row>
                  )}
              </div>
            </div>

            <div className="dashboard-data">
              <div className="table-header">
                <Row className="justify-content-md-center ">
                  <Col xs={6} sm={6} xl={6}>
                    <h5>Biggest Volume Change</h5>
                  </Col>
                  <Col xs={6} sm={6} xl={6}>
                    <div
                      className="view-all-dashboard"
                      onClick={() =>
                        (window.location.hash = "/biggest-volume-changes")
                      }
                    >
                      <FontAwesomeIcon icon={faEye} /> View All
                    </div>
                  </Col>
                </Row>
              </div>
              <div className="table-header-2">
                <Row className="justify-content-md-center main-markets-table-head">
                  <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                    <h5 className="main-head-coin-d">Coin</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>Price</h5>
                  </Col>
                  <Col xs={3} sm={3} xl={3} className="mb-3">
                    <h5>24H Change</h5>
                  </Col>
                  <Col xs={1} sm={1} xl={1} className="mb-3">
                    <h5>Add to favourites</h5>
                  </Col>
                  <Col xs={2} sm={2} xl={2} className="mb-3">
                    <h5>Market Cap</h5>
                  </Col>
                </Row>
              </div>

              <div className="main-table-cap">
                {this.state.isBVCDataLoading && <Loader />}
                {!this.state.isBVCDataLoading &&
                  this.state.BVCData &&
                  this.state.BVCData.map((data, index) => {
                    return (
                      <Row
                        key={data.symbol}
                        onClick={() =>
                          (window.location.hash =
                            "/coin/" + data.symbol + "/" + data.id)
                        }
                        className="justify-content-md-center"
                      >
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <table className="main-coin-name">
                            <tr>
                              <td>
                                <img src={data.image} alt={data.name} />
                              </td>
                              <td>
                                <h6>
                                  {data.name} <br /> <span>{data.symbol}</span>
                                </h6>{" "}
                              </td>
                            </tr>
                          </table>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.current_price)}
                          </p>
                        </Col>
                        <Col xs={3} sm={3} xl={3} className="mb-3">
                          <div className="chart-coin-name">
                            {data.market_cap_change_percentage_24h > 0 ? (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleUp}
                                  className={`text-success me-1`}
                                />
                                <span className={`text-success fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            ) : (
                              <h6 className="mb-1">
                                <FontAwesomeIcon
                                  icon={faAngleDown}
                                  className={`text-danger me-1`}
                                />
                                <span className={`text-danger fw-bold`}>
                                  {NumberFormat(
                                    data.market_cap_change_percentage_24h
                                  )}
                                  %
                                </span>
                              </h6>
                            )}
                            <Sparklines data={data.sparkline_in_7d.price}>
                              <SparklinesLine color={"rgb(18,199,255)"} />
                            </Sparklines>
                          </div>
                        </Col>
                        <Col xs={1} sm={1} xl={1} className="mb-3">
                          <MakeFavourites
                            webSocket={this.props.webSocket}
                            coin={data}
                            showText={false}
                          />
                        </Col>
                        <Col xs={2} sm={2} xl={2} className="mb-3">
                          <p className="PriceData">
                            ${NumberFormat(data.market_cap)}
                          </p>
                        </Col>
                      </Row>
                    );
                  })}

                {!this.state.isBVCDataLoading &&
                  this.state.BVCData.length === 0 && (
                    <Row className="justify-content-md-center">
                      <Col xs={12} sm={12} xl={12}>
                        <div className="no-data">
                          <h6>No data found for biggest volume change.</h6>
                        </div>
                      </Col>
                    </Row>
                  )}
              </div>
            </div>

            <div className="news">
              <div className="table-header-news">
                <Row className="justify-content-md-center ">
                  <Col xs={6} sm={6} xl={6}>
                    <h5>News</h5>
                  </Col>
                  <Col xs={6} sm={6} xl={6}>
                    <div
                      className="view-all-dashboard"
                      onClick={() => (window.location.hash = "/news")}
                    >
                      <FontAwesomeIcon icon={faEye} /> View All
                    </div>
                  </Col>
                </Row>
              </div>

              <div className="news-table">
                <Row className="justify-content-md-center">
                  {this.state.isNewsLoading && <Loader />}
                  {!this.state.isNewsLoading &&
                    this.state.newsData &&
                    this.state.newsData.map((data, index) => {
                      return (
                        <Col
                          key={data.link}
                          xs={12}
                          sm={6}
                          xl={6}
                          className="DetailsNEWS"
                          onClick={() => window.open(data.link, "_blank")}
                        >
                          <div className="row">
                            <div className="col-md-11 ">
                              <img src={data.imgURL} alt={data.title} />
                              <div className="news-content">
                                <p className="newsDate">
                                  {dateFormat(data.feedDate, "ddd, mmmm dS, yyyy")}
                                </p>
                                <p className="newsTitle">{data.title}</p>
                                <p className="newsAuthor">{data.source}</p>
                              </div>
                            </div>
                          </div>
                        </Col>
                      );
                    })}
                  {!this.state.isNewsLoading &&
                    this.state.newsData.length === 0 && (
                      <Col xs={12} sm={12} xl={12}>
                        <div className="row">
                          <div className="col-md-12">No news found.</div>
                        </div>
                      </Col>
                    )}
                </Row>
              </div>
              {/* <Modal isOpen={this.state.modal} toggle={this.toggle}>
          <ModalBody>{this.state.msg}</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={this.toggle}
              style={{ fontSize: "12px" }}
            >
              Okay
            </Button>

          </ModalFooter>
        </Modal> */}
              {/*  TODO: fix this
        <ScrollUpButton ContainerClassName="ScrollUpButton__Container" TransitionClassName="ScrollUpButton__Toggled">
               <p> <FontAwesomeIcon icon={faAngleUp} /> Scroll To Top  </p>
        </ScrollUpButton>  */}
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default Dashboard;
