// returns path param value followed by the given prefix
const getPathParam: (prefixPath: string) => string = (prefixPath: string) => {
    let startIndex: number = window.location.href.indexOf(prefixPath);
    if (startIndex === -1) {
        return "";
    }
    startIndex += prefixPath.length;

    let endIndex: number = window.location.href.indexOf("/", startIndex);

    if (endIndex === -1) {
        return window.location.href.substring(startIndex).replace("%20", " ");
    }
    return window.location.href.substring(startIndex, endIndex).replace("%20", " ");
};

export default getPathParam;