import { Col, Container, Navbar, Row } from "@themesberg/react-bootstrap";
import { Sparklines, SparklinesLine } from "react-sparklines";
import { faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Footer from "../shared/footer";
import Loader from "../shared/loader";
import { NumberFormat } from "../../common/number-format";
import React from "react";
import Sidebar from "../shared/sidebar";
import { Modal, ModalBody, ModalFooter,ModalHeader, Button } from "reactstrap";
interface AlertsState {
  modal: boolean,
  msg: String,
  alertsDataPrice: any[],
  alertsDataSignal: any[],
  isAlertsDataLoading: boolean,
  isAlertsDataSignalLoading: boolean,
  tokens: any,

  
}

interface AlertsProps {
    webSocket: any,
    checkLogin: ()=>void,
    checkPayment:()=>void
}

// dashboard component
class Alerts extends React.Component<AlertsProps, AlertsState> {

  constructor(props: AlertsProps) {
    super(props);
    this.props.checkLogin();
    props.checkPayment();
    this.state = {
      modal:false,
      msg:"",
      alertsDataPrice:[],
      alertsDataSignal:[],
      isAlertsDataLoading: true,
      isAlertsDataSignalLoading:true,
      tokens:{},

     
    };
  }

  update(){
    this.props.webSocket.emit("getAllAlerts", {
      userId: localStorage.getItem("verox_uid"),
      
    });
    
    this.props.webSocket.on("allAlerts", (response: any) => {
      if(response.data){
        const tokens = response.data.tokens;
        console.log('tokens',tokens);
        this.setState({tokens:tokens});
        this.props.webSocket.removeAllListeners("getAllAlerts");  // this is just one-time listener
        const data = {
          userId: localStorage.getItem("verox_uid"),
          coin: 'dogecoin'
        }
        console.log('send data=',data)
        this.props.webSocket.emit("getPriceAlerts", data);
        this.props.webSocket.emit("getSignalAlerts", {
          userId: localStorage.getItem("verox_uid"),
          coin: 'dogecoin'
        });
      }
    });

    
    this.props.webSocket.on("getAlerts", (response: any) => {
        console.log('response.data=',response.data)
        this.setState({alertsDataPrice: response.data, isAlertsDataLoading: false});
        
    });

    this.props.webSocket.on("getSignals", (response: any) => {

      
      const filteredData = response.data.filter((coin:any)=>coin.status);
      console.log('filteredData',filteredData);
      this.setState({alertsDataSignal: filteredData, isAlertsDataSignalLoading: false});
      
      
    });     


  }
  componentDidMount() {
    document.title = "Alerts | Verox";
    this.update();


  }

  toggle = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
    }));
    //window.location.reload();
  };
  delete_alert(index:number){
    var coin = this.state.alertsDataPrice[index];
    this.props.webSocket.emit("deletePriceAlerts", {
      userId:coin['userId'],
      coin: coin['coin'],
      price: coin['price'],
      trigger: coin['trigger']
    });
    this.setState({ msg:"Removed alert for "+coin['coin'],modal: true });
    //this.update();
    // this.props.webSocket.on("getSignals", (response: any) => {
    //   this.setState({alertsDataCheck: response.data, isAlertsDataCheckLoading: false});
    //   response.data.map((coin1: any)=>{
    //     this.setState(prevState => ({
    //       alertCoinsCheckData: [...prevState.alertCoinsCheckData, coin1.coin]
    //     }))
    //   })

    //   this.getCoinCheckData();
    //   this.props.webSocket.removeAllListeners("getSignals");
      
    // });   
  }
  
  delete_alert_check(index:number){
    var coin = this.state.alertsDataSignal[index];
    this.props.webSocket.emit("pushSignalAlert", {
      userId:coin['userId'],
      coin: coin['coin'],
      status:false
    });
    this.setState({ msg:"Removed alert for "+coin['coin'],modal: true });
    //this.update();
             
  }
 

  // getCoinData(){

  //   this.props.webSocket.emit("getTokensByIds", {ids: this.state.alertCoinsData});
  //   this.props.webSocket.on("tokensByIds", (response: any) => {
  //     //  this.setState({alertCoinsData:response.data})

  //     this.setState({coinsData:response.data})
  //       this.props.webSocket.removeAllListeners("tokensByIds");  // this is just one-time listener

  //       this.state.alertCoinsData.map((coin: any)=>{
  //             this.setState(prevState => ({
  //               coinsDataSequence: [...prevState.coinsDataSequence, (this.state.coinsData.find((co)=>co.id === coin))]
  //             }))
  //       })

        
  //   });

  // }

  render() {
    return (
        <>
        <Sidebar webSocket={this.props.webSocket}/>
          <div className="app-container-with-sidebar">
            <div className="component">
              <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4 style={{ fontSize: "2.25rem"}}>Price Alerts</h4>
              {/*<h4>Hi, {userInfo.username}!</h4>*/}
              <p>Your price alerts on your favorite coins</p>
            </div>
          </Container>
        </Navbar>

        
         

            <Row className="justify-content-md-center main-markets-table-head">
              <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                <h5 className="main-head-coin-d">Coin for Price Alert</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Price</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Trigger</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Action</h5>
              </Col>
            </Row>
          <div className="main-table-cap">
            {this.state.isAlertsDataLoading && (
              <Loader />
            )}
            {!this.state.isAlertsDataLoading && this.state.alertsDataPrice.length > 0 && this.state.alertsDataPrice.map((data,index) => {
             
           
              return(
                
                  <Row key={data.coin} className="justify-content-md-center" onClick={() => window.location.hash = ("/coin/"+this.state.tokens[data.coin].symbol+"/"+this.state.tokens[data.coin].id)}>
                      <Col xs={3} sm={3} xl={3} className="mb-3" >
                        <table className="main-coin-name">
                            <tr>
                                <td>{this.state.tokens.hasOwnProperty(data.coin) && <img src={this.state.tokens[data.coin].image} alt={data.name} />}</td>
                                <td>{this.state.tokens.hasOwnProperty(data.coin) && <h6>{(this.state.tokens[data.coin].name)}{this.state.tokens[data.coin].price} <br/> <span>{this.state.tokens[data.coin].symbol}</span></h6> }</td>
                            </tr>
                        </table>
                      </Col>
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                      <p className="PriceData">${NumberFormat(data.price)}</p>
                      </Col>
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                      <div className="chart-coin-name">
                          {data.trigger === "up" ? 
                              <h6 className="mb-1"><FontAwesomeIcon icon={faAngleUp} className={`text-success me-1`} />
                              <span className={`text-success fw-bold text-trigger`}>
                                   {(data.trigger)}
                              </span></h6>
                          :
                          <h6 className="mb-1"><FontAwesomeIcon icon={faAngleDown} className={`text-danger me-1`} />
                              <span className={`text-danger fw-bold text-trigger`}>
                                  {(data.trigger)}
                              </span></h6>
                          }
                          {/* <Sparklines data={data.sparkline_in_7d.price}>
                          <SparklinesLine color={"rgb(18,199,255)"} />
                          </Sparklines>         */}
                      </div>
                      </Col>
                      <Col xs={3} sm={3} xl={3} className="mb-3">
                      <p className="PriceData" style={{zIndex:999}} onClick={(e)=>{e.stopPropagation();this.delete_alert(index)}}>Delete</p>
                      </Col>
                  </Row>    
              )
            })}

            {!this.state.isAlertsDataLoading && this.state.alertsDataPrice.length === 0 && (
              <Row className="justify-content-md-center">
                <Col xs={12} sm={12} xl={12} >
                  <div className="no-data">                  
                    <h6>No data found for alerts.</h6>
                  </div>
                </Col>              
              </Row>    
            )}
          </div>

          <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
          <Container fluid className="px-0">
            <div className="header-class justify-content-between w-100">
              <h4 style={{ fontSize: "2.25rem"}}>Signal Alerts</h4>
              {/*<h4>Hi, {userInfo.username}!</h4>*/}
              <p>Your buy/sell alerts on your favorite coins</p>
            </div>
          </Container>
        </Navbar>
          <Row className="justify-content-md-center main-markets-table-head" style={{marginTop:'20px',maxHeight: '50px'}}>
              <Col xs={6} sm={6} xl={6} className="mb-6 main-head-coin-d">
                <h5 className="main-head-coin-d">Coin for Buy/Sell Alert</h5>
              </Col>
              <Col xs={6} sm={6} xl={6} className="mb-6">
                <h5>Action</h5>
              </Col>
            </Row>
          
          <div className="main-table-cap">
            {this.state.isAlertsDataSignalLoading  &&  (
              <Loader />
            )}
            {!this.state.isAlertsDataSignalLoading && this.state.alertsDataSignal.length > 0 && this.state.alertsDataSignal.map((data,index) => {
              return(
                  <Row key={data.symbol} className="justify-content-md-center" style={{maxHeight: '80px'}} >
                      <Col xs={6} sm={6} xl={6} className="mb-6" onClick={() =>  window.location.hash = ("/coin/"+this.state.tokens[data.coin].symbol+"/"+this.state.tokens[data.coin].id)}>
                        <table className="main-coin-name">
                            <tr>
                                <td>{this.state.tokens.hasOwnProperty(data.coin) && <img src={this.state.tokens[data.coin].image} alt={data.name} />}</td>
                                <td>{this.state.tokens.hasOwnProperty(data.coin) && <h6>{(this.state.tokens[data.coin].name)}{this.state.tokens[data.coin].price} <br/> <span>{this.state.tokens[data.coin].symbol}</span></h6> }</td>
                            </tr>
                        </table>
                        
                      </Col>

                      
                      <Col xs={6} sm={6} xl={6} className="mb-6">
                      <p className="PriceData" onClick={()=>this.delete_alert_check(index)}>Delete</p>
                      </Col>
                  </Row>    
              )
            })}

            {!this.state.isAlertsDataSignalLoading && this.state.alertsDataSignal.length === 0 && (
              <Row className="justify-content-md-center">
                <Col xs={12} sm={12} xl={12} >
                  <div className="no-data">                  
                    <h6>No data found for Buy/Sell alerts.</h6>
                  </div>
                </Col>              
              </Row>    
            )}
          </div>
        </div>
        <Footer/>
        </div>
        <Modal
          style={{ top: "240px" }}
          isOpen={this.state.modal}
          toggle={this.toggle}
        >
          <ModalBody style={{ fontSize: "35px" }}>
            {this.state.msg}
            <br />
            <br />
            <Button variant="secondary" className="btn-lg  mt-4" onClick={this.toggle} style={{
                background: "linear-gradient(to right, #9D6BFF, #FF6CCD)",
                outline:"none",
                padding: "15px 50px",
                borderRadius:"30px",
                color: "#fff",
                textTransform:"uppercase",
                fontSize: "15px",
                width: "200px",
                borderColor:"#2e3650",
              }}>Okay</Button>
          </ModalBody>
        </Modal>
      </>
    );
  }
}

export default Alerts;
