import {
  Col,
  Container,
  Form,
  Navbar,
  Row,
  Button,
} from "@themesberg/react-bootstrap";

import DOMPurify from "dompurify";
import Footer from "../shared/footer";
import React from "react";
import Sidebar from "../shared/sidebar";
import firebase from "firebase/app";

const myHTML = `
<ul class="li-link">
    <li>Please enter the valid email. </li>  
`;

const mySafeHTML = DOMPurify.sanitize(myHTML, {
  ALLOWED_TAGS: ["h1", "p", "span", "ul", "li", "a", "br"],
  ALLOWED_ATTR: ["style", "class", "href"],
});

interface ResetPasswordState {
  email: string;
}

interface ResetPasswordProps {
  webSocket: any;
  checkLogin: () => void;
}

// component for automated trading bot
class ResetPassword extends React.Component<
  ResetPasswordProps,
  ResetPasswordState
> {
  constructor(props: ResetPasswordProps) {
    super(props);
    // props.checkLogin();
    this.state = {
      email: "",
    };
  }

  componentDidMount() {
    document.title = "Reset Password | Verox";
  }

  resetPassword = () => {
      const auth = firebase.auth()
      auth.sendPasswordResetEmail(this.state.email)
      .then(res=>{
          window.alert("Password Reset Email sent to you, Please check")
          console.log("email sending",res)
      })
      .catch(error=>{
          const errorCode = error.code
          const errorMessage = error.message
          window.alert("Failed: " + errorMessage)
      })
  }

  render() {
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-50">
                  <h4 style={{ fontSize: "2.25rem" }}>Reset Password</h4>
                </div>
              </Container>
            </Navbar>

            <Row className="terms-of-use">
              <Col xs={12} sm={12} xl={12}>
                <div dangerouslySetInnerHTML={{ __html: mySafeHTML }} />
              </Col>
            </Row>
            <div className="trading-bot_page">
              <div className="bot-section d-flex flex-column p-5 mx-auto mt-5">
                <div className="inputs_container mt-4">
                  <Form>
                    <Form.Group>
                      <Form.Control
                        className="bot-inputs mb-4"
                        type="text"
                        name=""
                        placeholder="Enter Email"
                        autoComplete="off"
                        onChange={
                            e=>{
                                this.setState({
                                    ...this.state,
                                    email: e.target.value
                                })
                            }
                        }
                      />
                    </Form.Group>
                  </Form>
                  <div className="d-flex align-items-center flex-column mt-4">
                    <Button
                      variant="primary"
                      className="bot-form-btn mb-2"
                      onClick={() => {
                        // this.saveDetails();
                        this.resetPassword()
                      }}
                    >
                      Reset Password
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  }
}

export default ResetPassword;
