import { Col, Container, Form, InputGroup, Navbar, Row } from "@themesberg/react-bootstrap";
import { Sparklines, SparklinesLine } from "react-sparklines";
import { faAngleDown, faAngleUp, faSearch } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Footer from "../shared/footer";
import Loader from "../shared/loader";
import { NumberFormat } from "../../common/number-format";
import React from "react";
import Sidebar from "../shared/sidebar";
import MakeFavourites from './make-favourite'

interface MyFavouritesState {
  keywords: string,
  lastSearchKeyEnteredTimestamp: number,
  myFavouriteCoins: any[],
  ismyFavouriteCoinsLoading: boolean,
  myFavouriteCoinsData: any[]
}

interface MyFavouritesProps {
  webSocket: any,
  checkLogin: () => void,
  checkPayment:()=>void
}

class MyFavourites extends React.Component<MyFavouritesProps, MyFavouritesState> {
  constructor(props: MyFavouritesProps) {
    super(props);
    props.checkLogin();
    props.checkPayment();
    this.state = {
      keywords: "",
      lastSearchKeyEnteredTimestamp: 0,
      myFavouriteCoins: [],
      ismyFavouriteCoinsLoading: true,
      myFavouriteCoinsData: []
    };
  }

  componentDidMount() {
    document.title = "My Favoutites | Verox";
    this.props.webSocket.emit("getFavourite", {
      userId: localStorage.getItem("verox_uid")
    });
    this.props.webSocket.on("favourites", (data: any) => {
      //this.setState({ myFavouriteCoins: data.data });
      console.log('myFavouriteCoins',data.data)
      const myFavouriteCoins = data.data;
      this.props.webSocket.removeAllListeners("favourites");

      //Set in local storage for favourite
      if(myFavouriteCoins){
        localStorage.setItem('verox_fav', myFavouriteCoins.coins);

        if (myFavouriteCoins.coins && myFavouriteCoins.coins.length) {
          this.props.webSocket.emit("getTokensByIds", {
            ids: myFavouriteCoins.coins.join()
          });
          this.props.webSocket.on("tokensByIds", (data: any) => {
            this.props.webSocket.removeAllListeners("tokensByIds");
           
            this.setState({ myFavouriteCoinsData: data.data, ismyFavouriteCoinsLoading: false });
            console.log("removed token list");
          });
        } else {
          this.setState({ myFavouriteCoinsData: [], ismyFavouriteCoinsLoading: false });
        }
      }else{
        this.setState({ myFavouriteCoinsData: [], ismyFavouriteCoinsLoading: false });
      }

    });
    
  }

  search(event: any) {
    var currentTimestamp = Date.now();
    this.setState({ lastSearchKeyEnteredTimestamp: currentTimestamp });
    setTimeout(() => {
      if (currentTimestamp === this.state.lastSearchKeyEnteredTimestamp) {
        // value was not changed after the search was called the last time
        // control will not come here until user stops typing for more than 1.5 seconds
        this.setState({ keywords: event.target.value });
      }
    }, 1500);
  }

  handleFavouriteChange(dataFronFav: any) {
    //Set in local storage for favourite


    if (dataFronFav.coins.length) {
      this.props.webSocket.on("tokensByIds", (data: any) => {
        this.setState({ myFavouriteCoinsData: data.data });
        this.props.webSocket.removeAllListeners("tokensByIds");
      });
      this.props.webSocket.emit("getTokensByIds", {
        ids: dataFronFav.coins.join()
      });
    } else {
      this.setState({ myFavouriteCoinsData: [] });
    }
  }

  render() {
    return (
      <>
        <Sidebar webSocket={this.props.webSocket} />
        <div className="app-container-with-sidebar">
          <div className="component">
            <Navbar variant="dark" expanded className="ps-0 pe-2 pb-0">
              <Container fluid className="px-0">
                <div className="header-class justify-content-between w-100">
                  <h4 style={{ fontSize: "2.25rem" }}>Favourites</h4>
                  <p>Make your favourite coin for quick access</p>
                </div>
                <div className="justify-content-between w-100 search-form-class">
                  <div className="align-items-center">
                    <Form className="navbar-search">
                      <Form.Group id="topbarSearch">
                        <InputGroup className="input-group-merge search-bar">
                          <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
                          <Form.Control type="text" name="keywords" placeholder="Search In Most Favourites" autoComplete="off" onChange={(event) => this.search(event)} />
                        </InputGroup>
                      </Form.Group>
                    </Form>
                  </div>
                </div>

              </Container>
            </Navbar>

            <Row className="justify-content-md-center main-markets-table-head">
              <Col xs={3} sm={3} xl={3} className="mb-3 main-head-coin-d">
                <h5 className="main-head-coin-d">Coin</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Price</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>24H Change</h5>
              </Col>
              <Col xs={3} sm={3} xl={3} className="mb-3">
                <h5>Market Cap</h5>
              </Col>
            </Row>


            <div className="main-table-cap">
              {this.state.ismyFavouriteCoinsLoading
                ?
                <Loader />
                :
                <div>
                  {
                    this.state.myFavouriteCoinsData && this.state.myFavouriteCoinsData.length > 0 && this.state.myFavouriteCoinsData.filter(coin => (coin.name + coin.symbol).toLocaleLowerCase().includes(this.state.keywords.toLocaleLowerCase())).map((data: any, index: number) => {
                      return (
                        <Row key={index} className="justify-content-md-center" onClick={() => window.location.hash = ("/coin/"+data.symbol+"/"+data.id)}>
                          <Col xs={3} sm={3} xl={3} className="mb-3">
                          <table className="main-coin-name">
                            <tr>
                                <td><img src={data.image} alt={data.name} /></td>
                                <td><h6>{data.name} <br/> <span>{data.symbol}</span></h6> </td>
                            </tr>
                        </table>
                          </Col>
                          <Col xs={3} sm={3} xl={3} className="mb-3">
                            <p className="PriceData">${NumberFormat(data.current_price)}</p>
                          </Col>
                          <Col xs={3} sm={3} xl={3} className="mb-3">
                            <div className="chart-coin-name">
                              {data.market_cap_change_percentage_24h > 0 ?
                                <h6 className="mb-1"><FontAwesomeIcon icon={faAngleUp} className={`text-success me-1`} />
                                  <span className={`text-success fw-bold`}>
                                    {NumberFormat(data.market_cap_change_percentage_24h)}%
                                  </span></h6>
                                :
                                <h6 className="mb-1"><FontAwesomeIcon icon={faAngleDown} className={`text-danger me-1`} />
                                  <span className={`text-danger fw-bold`}>
                                    {NumberFormat(data.market_cap_change_percentage_24h)}%
                                  </span></h6>
                              }
                              <Sparklines data={data.sparkline_in_7d.price}>
                                <SparklinesLine color={"rgb(18,199,255)"} />
                              </Sparklines>

                            </div>
                          </Col>
                          <Col xs={1} sm={1} xl={1} className="mb-3">
                            <MakeFavourites webSocket={this.props.webSocket} coin={data} showText={false} getFavourite={this.handleFavouriteChange.bind(this)} callFromFavourite={true} />
                          </Col>
                          <Col xs={2} sm={2} xl={2} className="mb-3">
                            <p className="PriceData">${NumberFormat(data.market_cap)}</p>
                          </Col>
                        </Row>
                      )
                    })
                  }
                </div>
              }
            </div>
          </div>
          <Footer />
        </div>
      </>
    )
  }
}

export default MyFavourites;